import React, { useState, useEffect, useCallback } from "react";
import { goTo } from "route-lite";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";

import { StaticSelect, Input } from "../components";
import { useStore } from "../services/store";
import Login from "./login";
import rest from "../services/rest";
import { useToast } from "../services/toast";
import { useTranslation } from "../services/translate";

async function fetchEmail() {
  return new Promise((resolve) => {
    const { item } = Office?.context?.mailbox;
    if (!(item && item.to && item.to.getAsync)) resolve(null);

    item.to.getAsync((result) => {
      if (result.status === Office.AsyncResultStatus.Succeeded) {
        if (result.value && result.value.length) {
          const email = result.value[0].emailAddress;
          resolve(email);
        } else {
          resolve(null);
        }
      } else {
        resolve(null);
      }
    });
  });
}

const partnerModel = "com.axelor.apps.base.db.Partner";
const leadModel = "com.axelor.apps.crm.db.Lead";

async function fetchTemplates() {
  try {
    const language = await rest.search("com.axelor.apps.base.db.Language", {
      fields: ["id", "name", "code"],
      data: {
        _domain: "self.code = 'en'",
      },
    });

    const res = await rest.search("com.axelor.message.db.Template", {
      fields: ["id", "name"],
      data: {
        _domain: `(self.metaModel.fullName = '${partnerModel}' or self.metaModel.fullName = '${leadModel}') and self.isSystem != true and self.language.id = ${language.data[0].id}`,
      },
    });

    if (isValidResponse(res)) {
      const templates = res.data;
      return templates;
    }
  } catch (err) {
    console.log(err);
  }
}

async function fetchTemplateContent({ id, contact, isPartner, isLead, language = "en" }) {
  try {
    const action = await rest.action({
      model: "com.axelor.utils.db.Wizard",
      action: "action-generate-message-method-generate-message",
      data: {
        context: {
          _xTemplate: { id },
          language,
          _signal: "validateBtn",
          _model: "com.axelor.utils.db.Wizard",
          _objectId: contact.id,
          ...(isPartner
            ? {
                _tag: "Partner",
                _templateContextModel: partnerModel,
              }
            : {}),
          ...(isLead
            ? {
                _tag: "Lead",
                _templateContextModel: leadModel,
              }
            : {}),
        },
      },
    });

    if (!isValidResponse(action)) return;

    const flashMessage = action.data[0].flash;
    const flashRegex = /The email template model \((.)*\) is different than the entity generating the email \((.)*\)/gm;

    if (flashMessage && flashRegex.test(flashMessage)) {
      return { error: "Wrong template selected" };
    }

    const res = await rest.fetchId("com.axelor.message.db.Message", action.data[0].view.context["_showRecord"]);

    if (isValidResponse(res)) {
      const content = res.data[0].content;
      return { content };
    }
    return {};
  } catch (err) {
    console.log(err);
    return {};
  }
}

async function fetchContact(email) {
  try {
    const request = async (model) =>
      rest.search(model, {
        fields: ["name", "id", "version"],
        data: {
          criteria: [
            {
              fieldName: "emailAddress.address",
              value: email,
              operator: "=",
            },
          ],
          operator: "and",
        },
      });

    let res = await request(partnerModel);

    if (isValidResponse(res)) {
      const contact = res.data[0];
      return { contact, isPartner: true };
    }

    res = await request(leadModel);

    if (isValidResponse(res)) {
      const contact = res.data[0];
      return { contact, isLead: true };
    }

    return {};
  } catch (err) {
    console.log(err);
    return {};
  }
}

function getOptions(list = []) {
  return list.map((item) => {
    return { value: item.id, label: item.name };
  });
}

function isEmptyString(string) {
  return string === "";
}

function isEmpty(value) {
  return ["", undefined, null].includes(value);
}

function isValidResponse(res) {
  return res && res.status === 0 && Array.isArray(res.data) && res.data.length;
}

export default function Template() {
  const { state } = useStore();
  const { isLoggedIn } = state;

  const { toastSuccess, toastError } = useToast();

  const [email, setEmail] = useState("");
  const [templates, setTemplates] = useState([]);
  const [template, setTemplate] = useState("");
  const [loadingTemplateContent, setLoadingTemplateContent] = useState(false);

  const { t } = useTranslation();

  const handleLoadTemplate = useCallback(async () => {
    try {
      setLoadingTemplateContent(true);
      const email = await fetchEmail();
      if (!email) {
        setEmail("");
        toastError(t("Please select 'To' email address."));
        return;
      }

      setEmail(email);

      if (isEmpty(template)) {
        toastError(t("Please select template."));
        return;
      }

      const { contact, isPartner, isLead } = await fetchContact(email);

      if (!contact) {
        toastError(t("Recipient not available as a contact in AOS"));
        return;
      }

      const { content, error } = await fetchTemplateContent({ id: template, contact, isPartner, isLead });

      if (error) {
        toastError(error);
        return;
      }

      if (isEmptyString(content)) {
        toastSuccess(t("Template is empty string"));
        return;
      }

      if (!content) {
        toastError(t("Error loading template, Please try again."));
        return;
      }

      Office.context.mailbox.item.body.setAsync(content, { coercionType: Office.CoercionType.Html }, (result) => {
        if (Office.AsyncResultStatus.Succeeded) {
          toastSuccess(t("Loaded successfully"));
        } else {
          toastError(t("Error, Please try again."));
        }
      });
    } catch (err) {
      toastError(t("Error, Please try again."));
    } finally {
      setLoadingTemplateContent(false);
    }
  }, [toastError, toastSuccess, template]);

  const handleChangeTemplate = useCallback(
    (event) => {
      const template = event.target.value;
      setTemplate(template);
    },
    [email]
  );

  useEffect(() => {
    if (isLoggedIn === "false") {
      goTo(Login);
    } else {
      fetchEmail().then((email) => {
        email && setEmail(email);
      });
      fetchTemplates().then((templates) => {
        templates && setTemplates(templates);
      });
    }
  }, [isLoggedIn]);

  return (
    <Box display="flex" pr={2} pl={2}>
      <Box display="flex" flexDirection="column" width="100%" p={1} pt={3}>
        {email && <Input label="Email" value={email} handleChange={() => {}} disabled={true} />}
        <StaticSelect
          options={getOptions(templates)}
          label={t("Templates")}
          value={template}
          name="template"
          handleChange={handleChangeTemplate}
          disabled={loadingTemplateContent}
        />

        <Button disabled={loadingTemplateContent} onClick={handleLoadTemplate}>
          {t("Load Template")}
        </Button>
      </Box>
    </Box>
  );
}
