import React from "react";
import i18next from "i18next";
import { initReactI18next, I18nextProvider, useTranslation as i18nextUseTranslation } from "react-i18next";
import LanguageDetector from "i18next-browser-languagedetector";

import en from "../assets/locales/en/translations.json";
import fr from "../assets/locales/fr/translations.json";

const i18n = i18next.createInstance();
const lng = navigator.language.includes("fr") ? "fr" : "en";

const translations = {
  en,
  fr,
};

i18n
  .use(initReactI18next)
  .use(LanguageDetector)
  .init({
    lng,
    fallbackLng: "en",
    resources: {
      en: { translation: translations.en },
      fr: { translation: translations.fr },
    },
    interpolation: {
      escapeValue: false,
    },
    keySeparator: false,
  });

export default function Translate({ children }) {
  return <I18nextProvider i18n={i18n}>{children}</I18nextProvider>;
}

export function useTranslation() {
  return i18nextUseTranslation();
}
