import React from "react";
import moment from "moment";
import Box from "@material-ui/core/Box";
import List from "@material-ui/core/List";
import Typography from "@material-ui/core/Typography";
import ListItemText from "@material-ui/core/ListItemText";

import { ListItem } from "../list-item";
import { statusSelectList } from "./constant";

export function EventList({ events, onClick, translate }) {
  return (
    <Box component={List} width="100%">
      {events && Boolean(events.length) ? (
        events.map((event, i) => (
          <ListItem
            key={i}
            onClick={() => onClick && onClick(event)}
            title={event.subject}
            subtitle={
              <>
                <Typography variant="subtitle2" color="textPrimary">
                  {moment(event.startDateTime).format("DD/MM/YYYY HH:mm")} -{" "}
                  {moment(event.endDateTime).format("DD/MM/YYYY HH:mm")}
                </Typography>
                <Typography variant="body2">
                  {translate(statusSelectList.find((item) => item.value === event.statusSelect)?.label)}
                </Typography>
              </>
            }
            divider
          />
        ))
      ) : (
        <ListItem>
          <ListItemText primary={translate("No events available")} />
        </ListItem>
      )}
    </Box>
  );
}

export default EventList;
