import React, { useCallback, useMemo } from "react";
import { SnackbarProvider, useSnackbar } from "notistack";

function Toast(props) {
  const { children } = props;

  return (
    <SnackbarProvider
      autoHideDuration={1500}
      maxSnack={3}
      hideIconVariant
      anchorOrigin={{
        vertical: "bottom",
        horizontal: "right",
      }}
    >
      {children}
    </SnackbarProvider>
  );
}

export default Toast;

export function useToast() {
  const { enqueueSnackbar: openToast, closeSnackbar: closeToast } = useSnackbar();

  const toastSuccess = useCallback(
    (message, options) => {
      openToast(message, { variant: "success", ...options });
    },
    [openToast]
  );

  const toastError = useCallback(
    (message, options) => {
      openToast(message, { variant: "error", ...options });
    },
    [openToast]
  );

  const value = useMemo(
    () => ({
      openToast,
      toastSuccess,
      toastError,
      closeToast,
    }),
    [openToast, toastSuccess, toastError, closeToast]
  );

  return value;
}
