import React, { useCallback } from "react";

import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import { goTo } from "route-lite";

import { Input, Select as SelectComponent } from "../../../components";
import AddressForm from "../address-form";

const useStyles = makeStyles(() => ({
  container: {
    display: "flex",
    flexDirection: "column",
  },
}));

export default function ContactTab({ search, contact, unsaved, handleChange, setContact, disabled, translate }) {
  const { jobTitleFunction, fixedPhone, mobilePhone, emailAddress = {}, mainAddress } = contact;
  const classes = useStyles();

  const handleCreateAddress = useCallback(() => {
    goTo(AddressForm, {
      contact,
      unsaved,
      search,
    });
  }, [contact, search]);

  return (
    <Grid>
      <SelectComponent
        title={translate("Function")}
        model="com.axelor.apps.base.db.Function"
        value={jobTitleFunction}
        name="jobTitleFunction"
        data={contact}
        update={setContact}
        disabled={disabled}
      />
      <Grid className={classes.container}>
        <Input
          placeholder={translate("Fixed Phone")}
          value={fixedPhone || ""}
          name="fixedPhone"
          handleChange={handleChange}
          disabled={disabled}
        />
        <Input
          placeholder={translate("Mobile Phone")}
          value={mobilePhone || ""}
          name="mobilePhone"
          handleChange={handleChange}
          disabled={disabled}
        />
        <Input
          placeholder={translate("Email")}
          value={emailAddress.address || ""}
          name="emailAddress"
          handleChange={handleChange}
          disabled={disabled}
        />
      </Grid>
      <SelectComponent
        title={translate("Address")}
        model="com.axelor.apps.base.db.Address"
        optionLabel="fullName"
        value={mainAddress}
        name="mainAddress"
        data={contact}
        update={setContact}
        disabled={disabled}
      />
      <Box align="center">
        <Button onClick={handleCreateAddress}>{translate("Create New Address")}</Button>
      </Box>
    </Grid>
  );
}
