const KG_KEY = "AIzaSyBjV7z_Eu7zpeZiXHw92YRA_2V4oc26jaA";

export const getHeaders = (file, offset) => {
  const headers = {
    "X-File-Name": file.name,
    "X-File-Offset": offset,
    "X-File-Size": file.size,
    "X-File-Type": file.type,
  };
  if (file.id) {
    headers["X-File-Id"] = file.id;
  }
  return headers;
};

export async function uploadChunk(file, offset = 0) {
  const chunkSize = 100000;
  const end = offset + chunkSize < file.size ? offset + chunkSize : file.size;
  const blob = file.slice(offset, end);
  const headers = getHeaders(file, offset);
  const res = await rest.uploadFile(blob, headers);

  const { result } = res;
  if (result && result.id) {
    return result;
  } else {
    if (offset < file.size) {
      if (result.fileId) {
        file.id = result.fileId;
      }
      const response = await uploadChunk(file, chunkSize + offset);
      return response;
    }
  }
}

export const getPictureUrl = (picture) => {
  if (picture && picture.id) {
    let url = getServerURL();
    return `${url}/ws/rest/com.axelor.meta.db.MetaFile/${picture.id}/content/download`;
  }
  return undefined;
};

export async function getPartnerDefaultValues(partner = {}) {
  return rest
    .action({
      action: "action-group-base-partner-contact-name-onchange",
      model: "com.axelor.apps.base.db.Partner",
      data: {
        context: {
          ...partner,
          _domain: "self.isContact = false AND (self.isCustomer = true OR self.isProspect = true)",
          _id: null,
          _isCustomer: "true",
          _model: "com.axelor.apps.base.db.Partner",
          _source: "name",
          _viewName: "partner-customer-form",
          _viewType: "form",
          _views: [
            {
              type: "cards",
              name: "partner-cards",
            },
            {
              type: "grid",
              name: "partner-customer-grid",
            },
            {
              type: "form",
              name: "partner-customer-form",
            },
          ],
        },
      },
    })
    .then(({ data, status }) => {
      if (status === 0) {
        return data.reduce(
          (values, item) => {
            if (item.values) {
              return { ...values, ...item.values };
            } else {
              return values;
            }
          },
          { isCustomer: true }
        );
      } else {
        return {};
      }
    })
    .catch(() => ({}));
}

export async function getDefaultCompanySet() {
  return rest
    .action({
      action: "action-group-base-partner-onnew",
      data: {
        context: {
          "json-enhance": true,
          partnerAttrs: "{}",
          _domain: "self.isContact = false AND (self.isCustomer = true OR self.isProspect = true)",
          _id: null,
          _isCustomer: "true",
          _model: "com.axelor.apps.base.db.Partner",
          _source: "form",
          _viewName: "partner-customer-form",
          _viewType: "form",
          _views: [
            {
              type: "cards",
              name: "partner-cards",
            },
            {
              type: "grid",
              name: "partner-customer-grid",
            },
            {
              type: "form",
              name: "partner-customer-form",
            },
          ],
        },
      },
      model: "com.axelor.apps.base.db.PartnerAddress",
    })
    .then(({ data, status }) => {
      if (status === 0) {
        return data.reduce((values, item) => {
          if (item.attrs && item.attrs.companySet) {
            const { companySet } = item.attrs;
            return [
              ...values,
              ...(companySet["value"] || (companySet["value:add"] ? [companySet["value:add"]] : []) || []).map(
                ({ name, id, version }) => ({
                  name,
                  id,
                  version,
                })
              ),
            ];
          }
          return values;
        }, []);
      }
      return [];
    });
}

export function getCompanyNameFromEmail(email) {
  const trackName = email.split("@")[1];
  const names = trackName && trackName.split(".");
  const companyName = names && names[names.length - 2];

  return !["ac", "com", "in", "gmail", "info", "yahoo", "hotmail", "net", "org", "co", "gov"].includes(companyName)
    ? companyName
    : trackName;
}

export function getCompanyDetails(email) {
  if (!email) return;

  const companyName = getCompanyNameFromEmail(email);

  return fetch(
    `https://kgsearch.googleapis.com/v1/entities:search` +
      `?${qs.stringify({
        query: companyName,
        limit: 5,
      })}&key=${KG_KEY}`,
    {
      method: "GET",
      headers: {
        "Content-Type": "application/json",
      },
    }
  )
    .then((res) => res.json())
    .then((data) => {
      if (data.error) {
        throw new Error(data.error);
      }
      return data.itemListElement;
    })
    .then((items) => {
      const item = items.find((item) => {
        const { url } = item.result;
        return url && url.indexOf(companyName.toLowerCase()) !== -1;
      });

      return item && item.result;
    })
    .then((item) => {
      if (item) {
        const { detailedDescription, url, name, image } = item || {};
        const { articleBody, url: wikiUrl } = detailedDescription || {};
        const { contentUrl } = image || {};

        return {
          name,
          url,
          description: articleBody,
          wiki: wikiUrl,
          image: contentUrl,
        };
      }
    });
}

export default { uploadChunk, getHeaders, getPictureUrl, getDefaultCompanySet };
