import React, { useCallback, useState } from "react";
import { goTo } from "route-lite";
import Box from "@material-ui/core/Box";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import MuiMenu from "@material-ui/core/Menu";
import MenuItem from "@material-ui/core/MenuItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";

import LinkIcon from "@material-ui/icons/LaunchRounded";
import MenuIcon from "@material-ui/icons/Menu";
import LogoutIcon from "@material-ui/icons/ExitToApp";
import AddIcon from "@material-ui/icons/Add";
import ContactsIcon from "@material-ui/icons/Contacts";
import LeadsIcon from "@material-ui/icons/MyLocation";
import EventsIcon from "@material-ui/icons/Event";
import CopyEmailIcon from "@material-ui/icons/FileCopy";

import ContactList from "./contacts/contact-list";
import LeadList from "./leads/lead-list";
import EventList from "./events/event-list";
import CopyEmail from "./copy-email";
import Login from "./login";
import { Confirmation } from "../components";
import rest from "../services/rest";
import { getServerURL, setLoggedIn } from "../services/storage";
import { useStore } from "../services/store";
import { useConfirmation } from "../hooks";
import { useTranslation } from "../services/translate";

function Menu({ isCompose, update, anchorEl, onClose, onLogout }) {
  const { isConfirmationOpen, openConfirmation, closeConfirmation } = useConfirmation();
  const { t } = useTranslation();

  const logout = useCallback(() => {
    closeConfirmation();
    onLogout();
  }, [onLogout, closeConfirmation]);

  const handleMenuItemClick = (option) => () => {
    const { label, menu, view, update: shouldUpdate = true, onClick } = option;

    if (shouldUpdate) {
      update((draft) => {
        draft.moduleTitle = label;
        draft.menu = menu;
      });
    }
    onClose(); // close menu
    onClick && onClick(); // menu item on click handler
    view && goTo(view);
  };

  let options = [];

  if (!isCompose) {
    options = [
      { label: t("Contacts"), icon: ContactsIcon, view: ContactList, menu: 1 },
      { label: t("Leads"), icon: LeadsIcon, view: LeadList, menu: 2 },
      { label: t("Events"), icon: EventsIcon, view: EventList, menu: 3 },
      { label: t("Copy Email"), icon: CopyEmailIcon, view: CopyEmail, menu: 4 },
    ];
  }

  options.push({
    label: t("Logout"),
    icon: LogoutIcon,
    onClick: openConfirmation,
    update: false,
  });

  return (
    <>
      <MuiMenu
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "center",
        }}
        transformOrigin={{
          vertical: "top",
          horizontal: "center",
        }}
        keepMounted
        getContentAnchorEl={null}
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={onClose}
      >
        {options.map((option) => {
          const { label, icon: Icon } = option;
          return (
            <MenuItem key={label} onClick={handleMenuItemClick(option)}>
              <ListItemIcon>
                <Icon />
              </ListItemIcon>
              <ListItemText primary={label} />
            </MenuItem>
          );
        })}
      </MuiMenu>
      <Confirmation
        open={isConfirmationOpen}
        title={t("Logout Confirmation")}
        content={t("Are you sure, Do you want to logout?")}
        onConfirm={logout}
        onClose={closeConfirmation}
        translate={t}
      />
    </>
  );
}

function Header() {
  const [anchorEl, setAnchorEl] = useState(null);

  const { state, update } = useStore();
  const { email, isCompose, showAddButton } = state;

  const openMenu = useCallback((event) => {
    setAnchorEl(event.currentTarget);
  }, []);

  const closeMenu = useCallback(() => {
    setAnchorEl(null);
  }, []);

  const openServerInstance = useCallback(() => {
    window.open(getServerURL(), "_blank");
  }, []);

  const handleLogout = useCallback(() => {
    rest.logout();
    setLoggedIn(false);
    update((draft) => {
      draft.isLoggedIn = "false";
    });
    goTo(Login);
  }, [update]);

  const handleShowDetails = useCallback(() => {
    update((draft) => {
      draft.showDetails = true;
    });
  }, [update]);

  return (
    <Box display="flex">
      <AppBar position="fixed">
        <Toolbar disableGutters={true}>
          <Box display="flex" alignItems="center" flexGrow={1}>
            <IconButton color="inherit" onClick={openMenu}>
              <MenuIcon />
            </IconButton>
            {state.moduleTitle ? (
              <Typography color="secondary" variant="h6">
                <b>{state.moduleTitle}</b>
              </Typography>
            ) : (
              <img src="assets/logo_axelor.png" alt="logo" height="35" width="80" />
            )}
          </Box>
          {isCompose ? (
            <Box display="flex" alignItems="center">
              <IconButton color="inherit" onClick={openServerInstance}>
                <LinkIcon />
              </IconButton>
            </Box>
          ) : (
            showAddButton &&
            email && (
              <IconButton color="inherit" onClick={handleShowDetails}>
                <AddIcon />
              </IconButton>
            )
          )}
        </Toolbar>
      </AppBar>
      <Toolbar />
      <Menu onClose={closeMenu} onLogout={handleLogout} anchorEl={anchorEl} isCompose={isCompose} update={update} />
    </Box>
  );
}

export default Header;
