import React, { useEffect } from "react";
import Router from "route-lite";
import Box from "@material-ui/core/Box";

import { LoadingView } from "./components";
import View from "./containers/view";
import Header from "./containers/header";
import Template from "./containers/template";
import Store, { useStore } from "./services/store";
import Toast from "./services/toast";
import Theme from "./services/theme";
import Translate, { useTranslation } from "./services/translate";
import rest from "./services/rest";

function fetchLanguage() {
  return rest.get("/ws/app/info").then((res = { "user.lang": "en" }) => {
    return res["user.lang"];
  });
}

function App() {
  const { state } = useStore();
  const { isLoggedIn, isCompose } = state;
  const { i18n } = useTranslation();

  useEffect(() => {
    if (isLoggedIn === "false") return;
    fetchLanguage().then((lang) => {
      if (lang && i18n.language !== lang) {
        i18n.changeLanguage(lang);
      }
    });
  }, [isLoggedIn]);

  return (
    <Box>
      {isLoggedIn !== "false" && <Header />}
      {state.loading && <LoadingView />}
      <Router>{isCompose ? <Template /> : <View />}</Router>
    </Box>
  );
}

function WithServices(Component) {
  return function WithServices(props) {
    return (
      <Store>
        <Toast>
          <Theme>
            <Translate>
              <Component {...props} />
            </Translate>
          </Theme>
        </Toast>
      </Store>
    );
  };
}

export default WithServices(App);
