import React, { useState, useEffect, useRef } from "react";
import { goTo } from "route-lite";
import classnames from "classnames";
import Button from "@material-ui/core/Button";
import Box from "@material-ui/core/Box";
import Grid from "@material-ui/core/Grid";
import IconButton from "@material-ui/core/IconButton";
import { makeStyles } from "@material-ui/core/styles";

import ArrowBackIcon from "@material-ui/icons/ArrowBack";

import ContactForm from "./contacts/contact-form";
import LeadForm from "./leads/lead-form";
import EventForm from "./events/event-form";
import CompanyInformation from "./contacts/company-information";
import View from "./view";
import rest from "../services/rest";
import { Confirmation, StaticSelect } from "../components";
import { useStore } from "../services/store";
import useUnsaved from "../hooks/use-unsaved";
import { useTranslation } from "../services/translate";

const useStyles = makeStyles(() => ({
  root: {
    marginTop: 10,
    marginBottom: 10,
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
  },
  common: {
    boxShadow: "0px 3px 15px 0 rgba(0, 0, 0, 0.11)",
    marginTop: 15,
  },
  listItem: {
    padding: 24,
  },
  selection: {
    marginTop: 20,
  },
  contentContainer: {
    padding: "0px 15px",
    textAlign: "center",
  },
  mailSelection: {
    marginLeft: 20,
    marginRight: 20,
  },
  iconButton: {
    color: "black",
  },
  shadow: {
    boxShadow: "0px 3px 15px 0 rgba(0, 0, 0, 0.11)",
  },
}));

async function fetchContact({ email, name }) {
  try {
    const res = await rest.search("com.axelor.apps.base.db.Partner", {
      fields: [
        "emailAddress",
        "emailAddress.address",
        "simpleFullName",
        "name",
        "firstName",
        "mainPartner",
        "titleSelect",
        "fixedPhone",
        "jobTitleFunction",
        "mobilePhone",
        "mainAddress",
        "user",
        "team",
        "description",
        "companySet",
        "picture",
        "id",
        "version",
      ],
      data: {
        criteria: [
          {
            fieldName: "emailAddress.address",
            value: email,
            operator: "=",
          },
          {
            fieldName: "isContact",
            value: true,
            operator: "=",
          },
        ],
        operator: "and",
      },
    });

    if (res && res.status === 0 && Array.isArray(res.data) && res.data.length) {
      const contact = res.data[0];
      return {
        ...contact,
        emailAddress: {
          ...contact.emailAddress,
          address: contact["emailAddress.address"],
        },
      };
    }
  } catch (err) {
    console.log(err);
    return null;
  }
}

async function fetchLead({ email }) {
  try {
    const res = await rest.search("com.axelor.apps.crm.db.Lead", {
      fields: [
        "enterpriseName",
        "industrySector",
        "source",
        "primaryAddress",
        "primaryCity",
        "primaryCountry",
        "primaryPostalCode",
        "primaryState",
        "statusSelect",
        "emailAddress",
        "emailAddress.address",
        "name",
        "firstName",
        "titleSelect",
        "fixedPhone",
        "jobTitleFunction",
        "mobilePhone",
        "user",
        "team",
        "description",
        "picture",
        "webSite",
        "id",
        "version",
      ],
      data: {
        criteria: [
          {
            fieldName: "emailAddress.address",
            value: email,
            operator: "=",
          },
        ],
        operator: "and",
      },
    });

    if (res && res.status === 0 && Array.isArray(res.data) && res.data.length) {
      const lead = res.data[0];
      return {
        ...lead,
        emailAddress: {
          ...lead.emailAddress,
          address: lead["emailAddress.address"],
        },
      };
    }
  } catch (err) {
    console.log(err);
    return null;
  }
}

const getOptions = (list = []) => {
  return list
    .map((item) => item.emailAddress)
    .filter((item, index, array) => {
      return array.indexOf(item) === index; // filter unique
    })
    .map((email) => ({ value: email, label: email }));
};

export default function Details() {
  const classes = useStyles();
  const { state, update } = useStore();
  const { email, name, mailList, office365Id, mailInfo, menu } = state;

  const { unsavedRef, isUnsavedConfirmationOpen, openUnsavedConfirmation, closeUnsavedConfirmation, setUnsaved } =
    useUnsaved();

  const [contact, setContact] = useState(null);
  const [lead, setLead] = useState(null);
  const [fetchingContact, setFetchingContact] = useState(false);
  const [fetchingLead, setFetchingLead] = useState(false);
  const { t } = useTranslation();
  const handleRedirection = () => {
    if (unsavedRef.current) {
      openUnsavedConfirmation();
    } else {
      redirect();
    }
  };

  const redirect = () => {
    closeUnsavedConfirmation();
    update((draft) => {
      draft.showDetails = false;
    });
    goTo(View);
  };

  const handleEmailSelect = React.useCallback(
    (e) => {
      const mail = mailList.find((m) => m.emailAddress === e.target.value);
      if (mail) {
        update((draft) => {
          draft.email = mail.emailAddress;
          draft.name = mail.displayName;
        });
      }
    },
    [mailList]
  );

  const isContactPage = menu === 1;
  const isLeadPage = menu === 2;
  const isEventPage = menu === 3;

  useEffect(() => {
    if (isContactPage && (email || name)) {
      setFetchingContact(true);
      fetchContact({ email, name })
        .then(setContact)
        .finally(() => {
          setFetchingContact(false);
        });
    }
  }, [email, name, isContactPage]);

  useEffect(() => {
    if (isLeadPage && email) {
      setFetchingLead(true);
      fetchLead({ email })
        .then(setLead)
        .finally(() => {
          setFetchingLead(false);
        });
    }
  }, [email, isLeadPage]);

  return (
    <>
      <Box display="flex" pr={2} pl={2}>
        <Grid style={{ width: "100%" }}>
          <Grid className={classes.root}>
            <IconButton onClick={handleRedirection}>
              <ArrowBackIcon className={classes.iconButton} />
            </IconButton>
          </Grid>
          <Box className={classes.shadow} pt={2} pb={2}>
            <StaticSelect
              className={classnames([classes.selection, classes.mailSelection])}
              options={getOptions(mailList)}
              label={t("Email address")}
              value={email || ""}
              name="email"
              displayEmpty={!email}
              handleChange={handleEmailSelect}
            />
            {email && (
              <div className={classes.contentContainer}>
                {isContactPage && !contact && (
                  <Button
                    onClick={() => {
                      goTo(ContactForm, {
                        contact: {
                          emailAddress: { address: email },
                          name,
                          office365Id,
                        },
                        isBack: true,
                      });
                    }}
                  >
                    {t("Add Contact")}
                  </Button>
                )}
                {isLeadPage && !lead && (
                  <Button
                    onClick={() => {
                      goTo(LeadForm, {
                        lead: {
                          emailAddress: { address: email },
                          name,
                          office365Id,
                        },
                        isBack: true,
                      });
                    }}
                  >
                    {t("Add Lead")}
                  </Button>
                )}
                {isEventPage && (
                  <Button
                    onClick={() => {
                      goTo(EventForm, {
                        event: {
                          emailAddress: { address: email },
                          typeSelect: "0",
                          statusSelect: 1,
                          office365Id,
                          subject: mailInfo.subject,
                        },
                        isBack: true,
                      });
                    }}
                  >
                    {t("Add Event")}
                  </Button>
                )}
              </div>
            )}
          </Box>
          <Grid className={classes.common}>
            {isContactPage && contact && !fetchingContact && (
              <Grid>
                <ContactForm contact={contact} onChange={setUnsaved} />
              </Grid>
            )}
            {isLeadPage && lead && !fetchingLead && (
              <Grid>
                <LeadForm lead={lead} onChange={setUnsaved} />
              </Grid>
            )}
            {(isContactPage || isLeadPage) && (
              <Grid>
                <CompanyInformation email={email} />
              </Grid>
            )}
          </Grid>
        </Grid>
      </Box>
      <Confirmation
        open={isUnsavedConfirmationOpen}
        title={t("Unsaved changes")}
        content={t("There are unsaved changes, are you sure you want to redirect ?")}
        onConfirm={redirect}
        onClose={closeUnsavedConfirmation}
        translate={t}
      />
    </>
  );
}
