import React, { useState, useEffect, useCallback, useMemo } from "react";
import AutoComplete from "@material-ui/lab/Autocomplete";
import { TextField } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import classNames from "classnames";

import rest from "../../services/rest";

function useDebounceEffect(handler, interval) {
  const isMounted = React.useRef(false);
  React.useEffect(() => {
    if (isMounted.current) {
      const timer = setTimeout(() => handler(), interval);
      return () => clearTimeout(timer);
    }
    isMounted.current = true;
  }, [handler, interval]);
}

const useStyles = makeStyles({
  autoComplete: {
    marginBottom: 15,
    "& > div > label": {
      fontSize: 14,
    },
  },
  input: {
    fontSize: 14,
    color: "black",
  },
  label: {
    color: "black",
    fontSize: 14,
    "&#outlined-basic-label": {
      color: "black",
    },
  },
});

export function Select({
  name,
  model,
  title,
  optionLabel = "name",
  multiple = false,
  required,
  index,
  value,
  data,
  update,
  disabled,
  criteria: criteriaProp = [],
  criteriaIds,
  criteriaFieldName,
  criteriaOperator,
  type,
  operator = "or",
  className,
  fields = [],
}) {
  const [open, setOpen] = React.useState(false);
  const [options, setOptions] = useState([]);
  const [total, setTotal] = useState(0);
  const [searchText, setsearchText] = useState(null);
  const [loading, setLoading] = useState(false);
  const classes = useStyles();

  const fetchOptions = useCallback(
    (searchText = "", criteriaIds, params = { append: false }) => {
      setLoading(true);
      const criteria = [];
      if (searchText) {
        criteria.push({
          fieldName: optionLabel,
          operator: "like",
          value: searchText,
        });
        if (name === "address") {
          criteria.push({
            fieldName: optionLabel,
            operator: "like",
            value: searchText,
          });
          ["addressL2", "addressL3", "addressL4", "addressL5", "addressL6"].forEach((lane) => {
            criteria.push({
              fieldName: lane,
              operator: "like",
              value: searchText,
            });
          });
        }
      }

      if (criteriaFieldName) {
        criteria.push({
          fieldName: criteriaFieldName,
          value: criteriaIds,
          operator: criteriaOperator,
        });
      }

      const data = {
        criteria: [...criteria, ...criteriaProp],
        operator: operator,
      };

      return rest
        .search(model, {
          fields: ["name", "fullName", "subject", "metaFields", ...fields],
          data,
          limit: 10,
          offset: 0,
          ...params,
        })
        .then(({ data, total }) => {
          if (data && data.length) {
            setOptions((options) => (params.append ? [...options, ...data] : data));
            setTotal(total);
          }
        })
        .finally(() => {
          setLoading(false);
        });
    },
    [name, model, criteriaFieldName, criteriaOperator, operator, optionLabel]
  );

  const optionDebounceHandler = React.useCallback(() => {
    if (searchText) {
      fetchOptions(searchText, criteriaIds);
    }
  }, [fetchOptions, searchText, criteriaIds]);

  useDebounceEffect(optionDebounceHandler, 500);

  const loadMoreOptions = useCallback(() => {
    const searchTextAfterInitialValue = !multiple && value && value[optionLabel] === searchText;
    fetchOptions(!searchTextAfterInitialValue ? searchText : null, criteriaIds, {
      offset: options.length,
      append: true,
    });
  }, [fetchOptions, multiple, searchText, criteriaIds, options, optionLabel]);

  useEffect(() => {
    if (!open) {
      setOptions([]);
    }
  }, [open]);

  useEffect(() => {
    if (open) {
      fetchOptions(null, criteriaIds);
    }
  }, [fetchOptions, open, criteriaIds]);

  const hasMore = useMemo(() => options.length < total, [options, total]);

  return (
    <AutoComplete
      classes={{
        inputFocused: classes.input,
        clearIndicator: classes.input,
        popupIndicator: classes.input,
        disabled: classes.disabled,
      }}
      ListboxProps={{
        onScroll: (event) => {
          const { scrollTop, scrollHeight, clientHeight } = event.target;
          const atScrollEnd = scrollTop === scrollHeight - clientHeight;
          const shouldLoadMore = !loading && hasMore && atScrollEnd;
          if (shouldLoadMore) {
            loadMoreOptions();
          }
        },
      }}
      key={index}
      open={open}
      onOpen={() => {
        setOpen(true);
      }}
      onClose={() => {
        setOpen(false);
      }}
      clearOnEscape
      autoComplete
      loading={loading}
      className={classNames(classes.autoComplete, className)}
      options={options}
      multiple={multiple}
      required={required}
      value={value}
      onChange={(e, value) => {
        let values = value;
        if (type === "multiple") {
          values = value && value.filter((val, i, self) => i === self.findIndex((t) => t.id === val.id));
        }
        update({
          ...data,
          [name]: values,
        });
      }}
      name={name}
      disabled={disabled}
      onInputChange={(e, val) => setsearchText(val)}
      renderInput={(params) => (
        <TextField
          {...params}
          variant="outlined"
          label={title}
          fullWidth
          InputLabelProps={{
            className: classes && classes.label,
          }}
        />
      )}
      getOptionLabel={(option) => option[optionLabel] || ""}
    />
  );
}

export default Select;
