import React, { useCallback, useRef, useState } from "react";
import { goTo } from "route-lite";

import Box from "@material-ui/core/Box";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Button from "@material-ui/core/Button";

import ArrowBackIcon from "@material-ui/icons/ArrowBack";

import { Confirmation, Input, Select } from "../../components";
import ContactForm from "./contact-form";
import rest from "../../services/rest";
import { useToast } from "../../services/toast";
import useUnsaved from "../../hooks/use-unsaved";
import { useTranslation } from "../../services/translate";

function AddressForm({ contact, unsaved, search }) {
  const [saving, setSaving] = useState(false);
  const [values, setValues] = useState({
    addressL2: "",
    addressL3: "",
    addressL4: "",
    addressL5: "",
    addressL7Country: "",
    city: "",
    zip: "",
  });

  const { toastSuccess, toastError } = useToast();

  const { unsavedRef, isUnsavedConfirmationOpen, openUnsavedConfirmation, closeUnsavedConfirmation, setUnsaved } =
    useUnsaved();
  const { t } = useTranslation();

  const handleSubmit = useCallback(
    (event) => {
      event.preventDefault();
      setSaving(true);
      rest
        .add("com.axelor.apps.base.db.Address", values)
        .then(({ status, data }) => {
          if (status === 0 && data.length) {
            setSaving(false);
            toastSuccess(t("Address Saved"));
            goTo(ContactForm, {
              contact: { ...contact, mainAddress: data[0] },
              isBack: true,
              unsaved: true,
              initateContact: false,
              search,
            });
          } else {
            throw new Error(t("Error saving address"));
          }
        })
        .catch(({ message }) => {
          toastError(message);
          setSaving(false);
        });
    },
    [values, toastSuccess, toastError]
  );

  const handleChange = useCallback((event) => {
    const {
      target: { name, value },
    } = event;
    setUnsaved();
    setValues((values) => ({ ...values, [name]: value }));
  }, []);

  const handleChangeCity = useCallback((values) => {
    const _values = { ...values };
    const { city } = _values;

    if (city && city.zip) {
      _values.zip = city.zip;
    }
    setUnsaved();
    setValues({ ..._values });
  });

  const handleRedirection = useCallback(() => {
    if (unsavedRef.current) {
      openUnsavedConfirmation();
    } else {
      redirect();
    }
  }, []);

  const redirect = () => {
    closeUnsavedConfirmation();
    goTo(ContactForm, { contact, isBack: true, initateContact: false, unsaved, search });
  };

  return (
    <>
      <Box component="form" display="flex" flexDirection="column" pr={2} pl={2} onSubmit={handleSubmit}>
        <Box display="flex" alignItems="center">
          <Box component={IconButton} mr={1} onClick={handleRedirection} color="default">
            <ArrowBackIcon />
          </Box>
          <Typography variant="h6">{t("New Address")}</Typography>
        </Box>
        <Box component={Paper} height="100%" display="flex" flexDirection="column" p={1}>
          <Input
            placeholder={t("Recipient details")}
            value={values.addressL2}
            name="addressL2"
            handleChange={handleChange}
            disabled={saving}
          />
          <Input
            placeholder={t("Address precisions")}
            value={values.addressL3}
            name="addressL3"
            handleChange={handleChange}
            disabled={saving}
          />
          <Input
            placeholder={t("N and Street label")}
            value={values.addressL4}
            name="addressL4"
            handleChange={handleChange}
            disabled={saving}
            required
          />
          <Input
            placeholder={t("Distribution precisions (POB, Village...)")}
            value={values.addressL5}
            name="addressL5"
            handleChange={handleChange}
            disabled={saving}
          />
          <Select
            title={t("Country")}
            model="com.axelor.apps.base.db.Country"
            value={values.addressL7Country}
            name="addressL7Country"
            data={values}
            update={(values) => {
              setUnsaved();
              setValues(values);
            }}
            disabled={saving}
            required
          />
          <Select
            title={t("City")}
            model="com.axelor.apps.base.db.City"
            value={values.city}
            name="city"
            data={values}
            update={handleChangeCity}
            disabled={saving}
            criteriaIds={values?.addressL7Country?.id}
            criteriaFieldName="country.id"
            criteriaOperator="="
            operator="and"
            fields={["zip"]}
            required
          />
          <Input placeholder={t("Zip")} value={values.zip} name="zip" handleChange={handleChange} disabled={saving} />
          <Button type="submit" disabled={saving}>
            {t("Save")}
          </Button>
        </Box>
      </Box>
      <Confirmation
        open={isUnsavedConfirmationOpen}
        title="Unsaved changes"
        content="There are unsaved changes, are you sure you want to redirect ?"
        onConfirm={redirect}
        onClose={closeUnsavedConfirmation}
        translate={t}
      />
    </>
  );
}

export default AddressForm;
