import React, { useRef, useState } from "react";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import { makeStyles } from "@material-ui/core/styles";
import { goTo } from "route-lite";

import SaveIcon from "@material-ui/icons/Save";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import DeleteIcon from "@material-ui/icons/Delete";

import { Input, Select as SelectComponent, Tabs, StaticSelect, Confirmation } from "../../components";
import Leads from "./lead-list";
import rest from "../../services/rest";
import { useToast } from "../../services/toast";
import { useConfirmation } from "../../hooks";
import EventTab from "./tabs/event-tab";
import LeadDetailsTab from "./tabs/lead-tabs";
import useUnsaved from "../../hooks/use-unsaved";
import { useTranslation } from "../../services/translate";

const useStyles = makeStyles(() => ({
  iconButton: {
    color: "black",
  },
  common: {
    boxShadow: "0px 3px 15px 0 rgba(0, 0, 0, 0.11)",
    marginTop: 15,
    display: "flex",
    padding: 24,
    flexDirection: "column",
  },
  tab: {
    padding: 0,
  },
  container: {
    display: "flex",
    justifyContent: "space-between",
    marginTop: 10,
  },
  icon: {
    padding: 8,
  },
  typography: {
    textAlign: "left",
    marginBottom: 25,
  },
  toolbarEnd: {
    width: "100%",
    justifyContent: "flex-end",
    display: "flex",
  },
  main: {
    display: "flex",
    flexDirection: "column",
    boxShadow: "0px 3px 15px 0 rgba(0, 0, 0, 0.11)",
    marginTop: 10,
    overflow: "auto",
  },
  firstDetailsBlock: {
    boxShadow: "0px 3px 15px 0 rgba(0, 0, 0, 0.11)",
    padding: 24,
    display: "flex",
    flexDirection: "column",
  },
}));

export default function LeadForm({ lead: propLead, isBack, onChange: onChangeProp, search }) {
  const classes = useStyles();
  const [lead, setLead] = useState({ ...propLead });
  const {
    name,
    firstName,
    titleSelect,
    statusSelect,
    mobilePhone,
    fixedPhone,
    emailAddress = {},
    user,
    webSite,
  } = lead || {};

  const { isConfirmationOpen, openConfirmation, closeConfirmation } = useConfirmation();
  const {
    unsavedRef,
    isUnsavedConfirmationOpen,
    openUnsavedConfirmation,
    closeUnsavedConfirmation,
    setUnsaved,
    clearUnsaved,
  } = useUnsaved();

  const { toastSuccess, toastError } = useToast();
  const [saving, setSaving] = useState(false);

  const eventFormRef = useRef(null);

  const { t } = useTranslation();

  const handleChange = (e) => {
    setUnsaved();
    onChangeProp && onChangeProp(e);

    if (e.target.name !== "emailAddress") {
      setLead({
        ...lead,
        [e.target.name]: e.target.value,
      });
    } else {
      setLead({
        ...lead,
        emailAddress: { address: e.target.value },
      });
    }
  };

  const handleRedirection = () => {
    if (unsavedRef.current) {
      openUnsavedConfirmation();
    } else {
      redirect();
    }
  };

  const redirect = () => {
    closeUnsavedConfirmation();
    goTo(Leads, { search });
  };

  const onDelete = async () => {
    try {
      setSaving(true);
      closeConfirmation();
      let res = await rest.delete("com.axelor.apps.crm.db.Lead", lead.id);
      if (res && res.status === 0) {
        toastSuccess(t("Deleted Successfully"));
        clearUnsaved();
        handleRedirection();
      } else {
        toastError(res.data.message);
      }
    } catch (err) {
    } finally {
      setSaving(false);
    }
  };

  const onSave = async () => {
    try {
      setSaving(true);
      if (eventFormRef.current) {
        const isValid = await eventFormRef.current.save();
        if (!isValid) return;
      }

      if (!lead.name) {
        toastError(t("Name is required"));
        return;
      }

      const res =
        lead && lead.id
          ? await rest.update(
              "com.axelor.apps.crm.db.Lead",
              {
                ...lead,
              },
              lead.id
            )
          : await rest.add("com.axelor.apps.crm.db.Lead", {
              ...lead,
            });
      if (!res.data[0]) {
        toastError(t("Error occured! Please try again."));
        return;
      }

      clearUnsaved();
      toastSuccess(t("Saved Successfully"));

      handleRedirection();
    } catch (err) {
    } finally {
      setSaving(false);
    }
  };

  return (
    <>
      <Box display="flex" flexDirection="column" pr={2} pl={2}>
        <Grid className={classes.container}>
          {isBack && (
            <Grid>
              <IconButton onClick={handleRedirection} className={classes.icon}>
                <ArrowBackIcon className={classes.iconButton} />
              </IconButton>
            </Grid>
          )}
          <Grid className={classes.toolbarEnd}>
            {lead && lead.id && (
              <IconButton disabled={saving} onClick={openConfirmation} className={classes.icon}>
                <DeleteIcon className={classes.iconButton} />
              </IconButton>
            )}
            <IconButton disabled={saving} onClick={onSave} className={classes.icon}>
              <SaveIcon className={classes.iconButton} />
            </IconButton>
          </Grid>
        </Grid>
        <Grid className={classes.main}>
          <Grid className={classes.firstDetailsBlock}>
            <StaticSelect
              options={[
                { value: 1, label: t("M.") },
                { value: 2, label: t("Ms.") },
                { value: 3, label: t("Dr") },
                { value: 4, label: t("Prof.") },
              ]}
              label={t("Civility")}
              value={titleSelect || ""}
              name="titleSelect"
              handleChange={handleChange}
            />
            <Input placeholder={t("Name")} value={name || ""} handleChange={handleChange} name="name" />
            <Input placeholder={t("First Name")} value={firstName || ""} name="firstName" handleChange={handleChange} />
            <StaticSelect
              options={[
                { value: 1, label: t("New") },
                { value: 2, label: t("Assigned") },
                { value: 3, label: t("In process") },
                { value: 4, label: t("Converted") },
                { value: 5, label: t("Lost") },
              ]}
              label={t("Status")}
              value={statusSelect || ""}
              name="statusSelect"
              handleChange={handleChange}
            />
            <SelectComponent
              title={t("Assigned To")}
              model="com.axelor.auth.db.User"
              value={user}
              optionLabel="fullName"
              name="user"
              data={lead}
              update={setLead}
            />
          </Grid>
          <Grid className={classes.common}>
            <Typography variant="h6" className={classes.typography}>
              {t("Contact Details")}
            </Typography>
            <Input
              placeholder={t("Fixed Phone")}
              value={fixedPhone || ""}
              name="fixedPhone"
              handleChange={handleChange}
            />
            <Input
              placeholder={t("Mobile Phone")}
              value={mobilePhone || ""}
              name="mobilePhone"
              handleChange={handleChange}
            />
            <Input
              placeholder={t("Email")}
              value={emailAddress.address || ""}
              name="emailAddress"
              handleChange={handleChange}
            />
            <Input placeholder={t("Website")} value={webSite || ""} name="webSite" handleChange={handleChange} />
          </Grid>
          <Tabs
            tabs={[
              {
                id: 0,
                label: t("Contact"),
                children: (
                  <LeadDetailsTab lead={lead} handleChange={handleChange} setLead={setLead} className={classes.tab} />
                ),
              },
              lead &&
                lead.id && {
                  id: 1,
                  label: t("Events"),
                  children: (
                    <Box mt={2}>
                      <EventTab eventFormRef={eventFormRef} lead={lead} handleChange={handleChange} />
                    </Box>
                  ),
                },
            ]}
          />
        </Grid>
        <Confirmation
          open={isConfirmationOpen}
          title={t("Delete")}
          content={t("Are you sure you want to delete record?")}
          onClose={closeConfirmation}
          onConfirm={onDelete}
          translate={t}
        />
      </Box>
      <Confirmation
        open={isUnsavedConfirmationOpen}
        title={t("Unsaved changes")}
        content={t("There are unsaved changes, are you sure you want to redirect ?")}
        onConfirm={redirect}
        onClose={closeUnsavedConfirmation}
        translate={t}
      />
    </>
  );
}
