import React, { useCallback, useState, useImperativeHandle } from "react";
import classNames from "classnames";
import Grid from "@material-ui/core/Grid";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import { MuiPickersUtilsProvider, DateTimePicker } from "@material-ui/pickers";
import { makeStyles } from "@material-ui/core/styles";
import MomentUtils from "@date-io/moment";

import SaveIcon from "@material-ui/icons/Save";
import ArrowBackIcon from "@material-ui/icons/ArrowBack";
import DeleteIcon from "@material-ui/icons/Delete";

import { Input } from "../input";
import { Tabs } from "../tabs";
import { StaticSelect } from "../static-select";
import { Confirmation } from "../confirmation";
import { Select } from "../select";
import { statusSelectList, eventTypeList } from "./constant";
import { useConfirmation } from "../../hooks";

const useStyles = makeStyles(() => ({
  iconButton: {
    color: "black",
  },
  common: {
    boxShadow: "0px 3px 15px 0 rgba(0, 0, 0, 0.11)",
    marginTop: 15,
  },
  tab: {
    padding: 24,
  },
  paper: {
    display: "flex",
    flexDirection: "column",
  },
  main: {
    display: "flex",
    flexDirection: "column",
    boxShadow: "0px 3px 15px 0 rgba(0, 0, 0, 0.11)",
    marginTop: 10,
    overflow: "auto",
  },
  firstDetailsBlock: {
    boxShadow: "0px 3px 15px 0 rgba(0, 0, 0, 0.11)",
    padding: 24,
    display: "flex",
    flexDirection: "column",
  },
  toolbarEnd: {
    width: "100%",
    justifyContent: "flex-end",
    display: "flex",
  },
  container: {
    display: "flex",
    justifyContent: "space-between",
    marginTop: 10,
  },
  icon: {
    padding: 8,
  },
  imageWrapper: {
    width: "100%",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    marginTop: 15,
  },
  dateTimeInput: {
    marginBottom: 15,
  },
  disableIconButton: {
    color: "#bdbdbd",
  },
  error: {
    color: "red",
  },
}));

export function EventForm({
  saving,
  event,
  hidePartner,
  hideLead,
  onChange,
  onDelete,
  onBack,
  onSave,
  formRef,
  translate,
}) {
  const classes = useStyles();
  const [error, setError] = useState("");
  const { isConfirmationOpen, openConfirmation, closeConfirmation } = useConfirmation();
  const { startDateTime, statusSelect, subject, typeSelect, endDateTime, lead, contactPartner } = event;

  const handleDelete = useCallback(() => {
    closeConfirmation();
    onDelete && onDelete(event);
  }, [event, onDelete]);

  const handleSave = useCallback(() => {
    if (error) return false;

    if (onSave) return onSave(event);

    return true;
  }, [error, event, onSave]);

  const handleChangeContactPartner = useCallback(
    ({ contactPartner }) => {
      onChange({ target: { name: "contactPartner", value: contactPartner } });
    },
    [onChange]
  );

  const handleChangeLead = useCallback(
    ({ lead }) => {
      onChange({ target: { name: "lead", value: lead } });
    },
    [onChange]
  );

  const handleChangeDate = (name) => (value) => {
    onChange({ target: { value, name } });
    if (name === "startDateTime" && endDateTime && value.isAfter(endDateTime)) {
      setError(translate("Start date cannot be after end date"));
    } else if (name === "endDateTime" && startDateTime && value.isBefore(startDateTime)) {
      setError(translate("End date cannot be before start date"));
    } else {
      setError("");
    }
  };

  useImperativeHandle(
    formRef,
    () => ({
      save: async () => handleSave(),
    }),
    [handleSave]
  );

  return (
    <MuiPickersUtilsProvider utils={MomentUtils}>
      <Grid className={classes.container}>
        {onBack && (
          <Grid>
            <IconButton onClick={onBack} className={classes.icon}>
              <ArrowBackIcon className={classes.iconButton} />
            </IconButton>
          </Grid>
        )}
        <Grid className={classes.toolbarEnd}>
          {event && event.id && (
            <IconButton disabled={saving} onClick={openConfirmation} className={classes.icon}>
              <DeleteIcon className={classes.iconButton} />
            </IconButton>
          )}
          <IconButton disabled={error || saving} onClick={handleSave} className={classes.icon}>
            <SaveIcon className={classNames(classes.iconButton, { [classes.disableIconButton]: error })} />
          </IconButton>
        </Grid>
      </Grid>
      <Grid className={classes.main}>
        <Grid className={classes.firstDetailsBlock}>
          <StaticSelect
            options={eventTypeList.map((ev) => ({ ...ev, label: translate(ev.label) }))}
            label={translate("Type")}
            value={typeSelect}
            name="typeSelect"
            handleChange={onChange}
          />
          <StaticSelect
            options={statusSelectList.map((ev) => ({ ...ev, label: translate(ev.label) }))}
            label={translate("Status")}
            value={statusSelect}
            name="statusSelect"
            handleChange={onChange}
          />
          <Input
            label={translate("Subject")}
            InputLabelProps={{
              shrink: true,
            }}
            value={subject}
            handleChange={onChange}
            name="subject"
            multiline={true}
          />
          <DateTimePicker
            InputLabelProps={{
              shrink: true,
            }}
            autoOk
            className={classes.dateTimeInput}
            inputVariant="outlined"
            label={translate("Start Date")}
            name="startDateTime"
            format="MM/DD/YYYY hh:mm"
            value={startDateTime || null}
            onChange={handleChangeDate("startDateTime")}
          />
          <DateTimePicker
            InputLabelProps={{
              shrink: true,
            }}
            autoOk
            className={classes.dateTimeInput}
            inputVariant="outlined"
            label={translate("End Date")}
            name="endDateTime"
            {...(startDateTime ? { minDate: startDateTime } : {})}
            format="MM/DD/YYYY hh:mm"
            value={endDateTime || null}
            onChange={handleChangeDate("endDateTime")}
          />
          {error && (
            <Box component={Typography} mt={1} variant="body2" className={classes.error}>
              {error}
            </Box>
          )}
        </Grid>
        <Tabs
          className={classes.tab}
          tabs={[
            {
              id: 0,
              label: translate("References"),
              children: (
                <Grid>
                  {!hidePartner && (
                    <Select
                      model="com.axelor.apps.base.db.Partner"
                      title={translate("Contact")}
                      value={contactPartner || {}}
                      optionLabel="fullName"
                      name="contactPartner"
                      data={{}}
                      update={handleChangeContactPartner}
                      criteria={[{ fieldName: "isContact", value: true, operator: "=" }]}
                    />
                  )}
                  {!hideLead && (
                    <Select
                      model="com.axelor.apps.crm.db.Lead"
                      title={translate("Lead")}
                      value={lead || {}}
                      optionLabel="fullName"
                      name="lead"
                      data={{}}
                      update={handleChangeLead}
                    />
                  )}
                </Grid>
              ),
            },
          ]}
        />
      </Grid>
      <Confirmation
        open={isConfirmationOpen}
        title={translate("Delete")}
        content={translate("Are you sure you want to delete record?")}
        onConfirm={handleDelete}
        onClose={closeConfirmation}
        translate={translate}
      />
    </MuiPickersUtilsProvider>
  );
}

export default EventForm;
