import React from "react";
import { makeStyles } from "@material-ui/core/styles";

import PersonIcon from "@material-ui/icons/Person";

const useStyles = makeStyles(() => ({
  container: {
    borderRadius: "50%",
    display: "inline-block",
    position: "relative",
    padding: 6,
    cursor: "pointer",
    background: "linear-gradient(270deg, #3fa1a9, #79f1a4)",
    marginBottom: 25,
  },
  wrapper: {
    position: "relative",
    width: 80,
    height: 80,
    overflow: "hidden",
    borderRadius: "50%",
    display: "flex",
    justifyContent: "center",

    "&:before": {
      content: '"Add Photo"',
      fontSize: 10,
      position: "absolute",
      paddingTop: 80,
      top: "80%",
      left: "50%",
      transform: "translate(-50%, -50%)",
      textAlign: "center",
      color: "#63d3a6",
      width: 130,
      height: 225,
      borderRadius: "50%",
      opacity: 0,
      transition: "0.5s ease",
      backgroundColor: "#fff",
    },
    "&:hover": {
      "&:before": {
        opacity: 1,
      },
    },
  },
  image: {
    width: "auto",
    height: "100%",
  },
  input: {
    display: "none",
  },
  icon: {
    color: "white",
    fontSize: "50px !important",
    height: "100% !important",
  },
}));

export function ImageUpload({ url, name, onChange }) {
  const classes = useStyles();

  return (
    <label className={classes.container}>
      <div className={classes.wrapper}>
        {url ? <img src={url} alt={name} className={classes.image} /> : <PersonIcon className={classes.icon} />}
      </div>
      <input type="file" id="photo-upload" className={classes.input} onChange={onChange} />
    </label>
  );
}

export default ImageUpload;
