import React from "react";

import { Grid, Select, MenuItem, InputLabel, FormControl } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import classnames from "classnames";

const useStyles = makeStyles((theme) => ({
  root: {
    textAlign: "left",
  },
  formControl: {
    minWidth: 120,
    marginBottom: 15,
    width: "100%",
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  disableSelect: {
    color: "black",
  },
}));

export function StaticSelect({ options, label, disabled, value, handleChange, name, className, displayEmpty }) {
  const classes = useStyles();
  const [labelWidth, setLabelWidth] = React.useState(0);
  const inputLabel = React.useRef(null);

  React.useEffect(() => {
    setLabelWidth(inputLabel.current.offsetWidth);
  }, []);

  return (
    <Grid className={classnames(classes.root, className)}>
      <FormControl variant="outlined" className={classes.formControl}>
        <InputLabel
          ref={inputLabel}
          id="demo-simple-select-outlined-label"
          style={{ color: disabled ? "rgba(0, 0, 0, 0.54)" : "black" }}
        >
          {label}
        </InputLabel>
        <Select
          labelId="demo-simple-select-outlined-label"
          id="demo-simple-select-outlined"
          value={value}
          onChange={handleChange}
          labelWidth={labelWidth}
          name={name}
          disabled={disabled}
          classes={{
            disabled: classes.disableSelect,
          }}
          displayEmpty={displayEmpty}
        >
          {options.map(({ value, label }) => (
            <MenuItem key={value} value={value}>
              {label}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </Grid>
  );
}

export default StaticSelect;
