import React, { useCallback, useEffect, useRef, useState } from "react";
import { goTo } from "route-lite";
import Box from "@material-ui/core/Box";

import { Confirmation, EventForm } from "../../components";
import rest from "../../services/rest";
import Events from "./event-list";
import { useStore } from "../../services/store";
import { useToast } from "../../services/toast";
import useUnsaved from "../../hooks/use-unsaved";
import { useTranslation } from "../../services/translate";

function isValidResponse(res) {
  return res && res.status === 0 && Array.isArray(res.data) && res.data.length;
}

export async function fetchMainPartner({ contact }) {
  if (!contact) return;
  try {
    const res = await rest.fetchId("com.axelor.apps.base.db.Partner", contact.id);
    if (isValidResponse(res)) {
      const contact = res.data[0];
      return contact && contact.mainPartner;
    }
  } catch (err) {
    console.log(err);
  }
}

async function fetchPartner({ email }) {
  try {
    const res = await rest.search("com.axelor.apps.base.db.Partner", {
      fields: ["id", "version", "name", "fullName"],
      data: {
        criteria: [
          {
            fieldName: "emailAddress.address",
            value: email,
            operator: "=",
          },
          {
            fieldName: "isContact",
            value: true,
            operator: "=",
          },
        ],
        operator: "and",
      },
    });
    if (isValidResponse(res)) {
      const partner = res.data[0];
      return partner;
    }
  } catch (err) {
    console.log(err);
  }
}

async function fetchLead({ email }) {
  try {
    const res = await rest.search("com.axelor.apps.crm.db.Lead", {
      fields: ["id", "version", "name", "fullName"],
      data: {
        criteria: [
          {
            fieldName: "emailAddress.address",
            value: email,
            operator: "=",
          },
        ],
        operator: "and",
      },
    });

    if (isValidResponse(res)) {
      const lead = res.data[0];
      return lead;
    }
  } catch (err) {
    console.log(err);
    return null;
  }
}

export default function EventFormContainer({ event: eventProp, isBack, search }) {
  const [event, setEvent] = useState({
    typeSelect: "",
    statusSelect: "",
    subject: "",
    ...eventProp,
  });
  const { emailAddress } = event;
  const { t } = useTranslation();
  const { state, update } = useStore();
  const { toastSuccess, toastError } = useToast();

  const {
    unsavedRef,
    isUnsavedConfirmationOpen,
    openUnsavedConfirmation,
    closeUnsavedConfirmation,
    setUnsaved,
    clearUnsaved,
  } = useUnsaved();

  const [saving, setSaving] = useState(false);

  const handleChange = useCallback(
    (e) => {
      const { name, value } = e.target;
      setUnsaved();
      setEvent({
        ...event,
        [name]: value,
      });

      if (name === "contactPartner") {
        fetchMainPartner({ contact: value }).then((partner) => {
          if (partner) {
            setEvent((event) => ({ ...event, partner }));
          }
        });
      }
    },
    [event]
  );

  const handleDelete = useCallback(async (event) => {
    try {
      setSaving(true);
      const res = await rest.delete("com.axelor.apps.crm.db.Event", event.id);
      if (res && res.status === 0) {
        toastSuccess(t("Deleted Successfully"));
        clearUnsaved();
        redirect();
      } else {
        toastError(res.data.message);
      }
    } catch (err) {
    } finally {
      setSaving(false);
    }
  }, []);

  const handleRedirection = () => {
    if (unsavedRef.current) {
      openUnsavedConfirmation();
    } else {
      redirect();
    }
  };

  const redirect = () => {
    closeUnsavedConfirmation();
    goTo(Events, { search });
  };

  const handleSave = async (event) => {
    try {
      setSaving(true);

      if (!event.subject) {
        toastError(t("Subject is required"));
        return;
      }

      if (!(event.startDateTime && event.startDateTime)) {
        toastError(t("Start date time & End date time is required"));
        return;
      }

      const eventObj = {
        ...event,
        office365Id: state.office365Id,
      };

      update((draft) => {
        draft.loading = true;
      });

      const res =
        eventObj && eventObj.id
          ? await rest.update(
              "com.axelor.apps.crm.db.Event",
              {
                ...eventObj,
              },
              eventObj.id
            )
          : await rest.add("com.axelor.apps.crm.db.Event", {
              ...eventObj,
            });
      if (!res) {
        toastError(t("Error occured! Please try again."));
        return;
      }

      toastSuccess(t("Saved Successfully"));
      update((draft) => {
        draft.loading = false;
        draft.showDetails = false; // redirect to list after save
      });
      clearUnsaved();
      handleRedirection();
    } catch (err) {
    } finally {
      setSaving(false);
    }
  };

  useEffect(() => {
    const email = emailAddress && emailAddress.address;
    if (!email) return;

    fetchPartner({ email }).then((partner) => {
      if (partner) {
        setEvent((event) => ({ ...event, partner }));
      }
    });

    fetchLead({ email }).then((lead) => {
      if (lead) {
        setEvent((event) => ({ ...event, lead }));
      }
    });
  }, [emailAddress]);

  return (
    <>
      <Box display="flex" flexDirection="column" pr={2} pl={2}>
        <EventForm
          event={event}
          onChange={handleChange}
          onDelete={handleDelete}
          onBack={isBack && handleRedirection}
          onSave={handleSave}
          saving={saving}
          translate={t}
        />
      </Box>
      <Confirmation
        open={isUnsavedConfirmationOpen}
        title={t("Unsaved changes")}
        content={t("There are unsaved changes, are you sure you want to redirect?")}
        onConfirm={redirect}
        onClose={closeUnsavedConfirmation}
        translate={t}
      />
    </>
  );
}
