import React from "react";
import { Grid, Typography, Tabs as MuiTabs, Tab } from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";

const StyledTab = withStyles((theme) => ({
  root: {
    textTransform: "none",
    minWidth: 72,
    fontWeight: theme.typography.fontWeightRegular,
    marginRight: theme.spacing(4),
    "&:hover": {
      color: "#40a9ff",
      opacity: 1,
    },
    "&$selected": {
      color: "#1890ff",
      fontWeight: theme.typography.fontWeightMedium,
    },
    "&:focus": {
      color: "#40a9ff",
    },
  },
  selected: {},
}))((props) => <Tab disableRipple {...props} />);

function TabPanel(props) {
  const { children, value, index, className, ...other } = props;
  return (
    <Typography
      component="div"
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && <Grid className={className}>{children}</Grid>}
    </Typography>
  );
}

export function Tabs({ tabs: tabsProp, disabled, className }) {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const tabs = tabsProp.filter(Boolean);

  return (
    <Grid style={{ boxShadow: "0px 3px 15px 0 rgba(0, 0, 0, 0.11)", marginTop: 15 }}>
      <MuiTabs value={value} indicatorColor="primary" textColor="primary" onChange={handleChange}>
        {tabs.map((tab) => (
          <StyledTab label={tab.label} key={tab.id} disabled={disabled} />
        ))}
      </MuiTabs>
      {tabs.map((tab) => (
        <TabPanel
          value={value}
          index={tab.id}
          key={tab.id}
          style={{ display: "flex", flexDirection: "column", marginTop: 0 }}
          className={tab.className || className}
        >
          {tab.children}
        </TabPanel>
      ))}
    </Grid>
  );
}

export default Tabs;
