import React, { useState, useEffect, useCallback, useRef } from "react";
import Box from "@material-ui/core/Box";
import Grid from "@material-ui/core/Grid";
import IconButton from "@material-ui/core/IconButton";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";

import { Save, ArrowBack, Delete } from "@material-ui/icons";
import { makeStyles } from "@material-ui/core/styles";
import { goTo } from "route-lite";

import {
  Input,
  Select as SelectComponent,
  Tabs,
  StaticSelect,
  LoadingView,
  Confirmation,
  ImageUpload,
} from "../../components";
import { uploadChunk, getPictureUrl, getPartnerDefaultValues, getDefaultCompanySet, getCompanyDetails } from "./utils";
import rest from "../../services/rest";
import ContactList from "./contact-list";
import ContactTab from "./tabs/contact-tab";
import OthersTab from "./tabs/others-tab";
import EventTab from "./tabs/event-tab";
import { useToast } from "../../services/toast";

import { useConfirmation } from "../../hooks";
import useUnsaved from "../../hooks/use-unsaved";
import { useTranslation } from "../../services/translate";

const useStyles = makeStyles(() => ({
  iconButton: {
    color: "black",
  },
  common: {
    boxShadow: "0px 3px 15px 0 rgba(0, 0, 0, 0.11)",
    marginTop: 15,
  },
  tab: {
    padding: 24,
  },
  main: {
    display: "flex",
    flexDirection: "column",
    boxShadow: "0px 3px 15px 0 rgba(0, 0, 0, 0.11)",
    marginTop: 10,
    overflow: "auto",
  },
  firstDetailsBlock: {
    boxShadow: "0px 3px 15px 0 rgba(0, 0, 0, 0.11)",
    padding: 24,
    display: "flex",
    flexDirection: "column",
  },
  toolbarEnd: {
    width: "100%",
    justifyContent: "flex-end",
    display: "flex",
  },
  container: {
    display: "flex",
    justifyContent: "space-between",
    marginTop: 10,
  },
  icon: {
    padding: 8,
  },
  imageWrapper: {
    width: "100%",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    marginTop: 15,
  },
  eventTab: {
    padding: 0,
  },
}));

const MainCompanyCriteria = [
  {
    fieldName: "partnerTypeSelect",
    value: 1,
    operator: "=",
  },
  {
    fieldName: "isContact",
    value: false,
    operator: "=",
  },
];

export default function ContactForm({
  contact: propContact,
  unsaved: unsavedProp,
  isBack,
  onChange: onChangeProp,
  initateContact = true,
  search,
}) {
  const classes = useStyles();
  const [contact, setContact] = useState({
    mainPartner: { partnerTypeSelect: undefined, name: "", webSite: "", description: "", ...propContact.mainPartner },
    isNewCompany: false,
    ...propContact,
  });
  const { id, name, firstName, mainPartner, titleSelect, picture, companySet, isNewCompany, emailAddress } =
    contact || {};
  const [imageUrl, setUrl] = useState(getPictureUrl(picture));
  const [file, setFile] = useState("");
  const [criteriaIds, setCriteriaIds] = useState(null);
  const [loading, setLoading] = useState(true);
  const [saving, setSaving] = useState(false);

  const eventFormRef = useRef(null);

  const {
    unsavedRef,
    isUnsavedConfirmationOpen,
    openUnsavedConfirmation,
    closeUnsavedConfirmation,
    setUnsaved,
    clearUnsaved,
    updateUnsaved,
  } = useUnsaved({ initialValue: unsavedProp });

  const { isConfirmationOpen, openConfirmation, closeConfirmation } = useConfirmation();

  const { toastSuccess, toastError } = useToast();
  const { t } = useTranslation();
  const handleChange = (e) => {
    const { name, value, checked } = e.target;

    setUnsaved();

    onChangeProp && onChangeProp(e);

    setContact((contact) => {
      if (name === "emailAddress") {
        return { ...contact, emailAddress: { address: value } };
      } else if (name === "isNewCompany") {
        return {
          ...contact,
          [name]: checked,
          mainPartner: {
            partnerTypeSelect: 1,
          },
        };
      } else if (name.includes("mainPartner.")) {
        return { ...contact, mainPartner: { ...contact.mainPartner, [name.split(".")[1]]: value } };
      } else {
        return { ...contact, [name]: value };
      }
    });
  };

  const onDelete = async () => {
    try {
      setSaving(true);
      closeConfirmation();
      let res = await rest.delete("com.axelor.apps.base.db.Partner", contact.id);
      if (res && res.status === 0) {
        toastSuccess(t("Deleted Successfully"));
        clearUnsaved();

        handleRedirection();
      } else {
        toastError(res.data.message);
      }
    } catch (err) {
    } finally {
      setSaving(false);
    }
  };

  const handleRedirection = () => {
    if (unsavedRef.current) {
      openUnsavedConfirmation();
    } else {
      redirect();
    }
  };

  const redirect = () => {
    closeUnsavedConfirmation();
    goTo(ContactList, { search });
  };

  const onSave = async () => {
    try {
      setSaving(true);
      if (eventFormRef.current) {
        const isValid = await eventFormRef.current.save();
        if (!isValid) return;
      }

      if (!contact.name) {
        toastError(t("Name is required"));
        return;
      }

      if (contact.isNewCompany && !(contact.mainPartner.name && contact.mainPartner.partnerTypeSelect)) {
        toastError(t("New company name and type is required"));
        return;
      }

      if (contact.isNewCompany && !(contact.companySet && contact.companySet.length)) {
        toastError(t("Select company associated for creating new company"));
        return;
      }

      let mainPartnerValues = {};
      if (contact.isNewCompany) {
        mainPartnerValues = await getPartnerDefaultValues(contact.mainPartner);
        mainPartnerValues.companySet = companySet;
      } else {
        if (!(contact.mainPartner && (contact.mainPartner.name || contact.mainPartner.fullName))) {
          delete contact.mainPartner; // empty partner
        }
      }

      let picture;
      if (file) {
        picture = await uploadChunk(file);
      }

      const res =
        contact && contact.id
          ? await rest.update(
              "com.axelor.apps.base.db.Partner",
              {
                ...contact,
                mainPartner: contact.mainPartner ? { ...contact.mainPartner, ...mainPartnerValues } : null,
                picture: picture ? { id: picture.id } : contact.picture,
              },
              contact.id
            )
          : await rest.add("com.axelor.apps.base.db.Partner", {
              ...contact,
              mainPartner: contact.mainPartner ? { ...contact.mainPartner, ...mainPartnerValues } : null,
              isContact: true,
              picture: picture ? { id: picture.id } : contact.picture,
            });

      if (!res) {
        toastError(t("Error occured! Please try again."));
        return;
      }

      clearUnsaved();
      toastSuccess(t("Saved Successfully"));
      handleRedirection();
    } catch (err) {
    } finally {
      setSaving(false);
    }
  };

  const uploadImage = (e) => {
    e.preventDefault();
    const reader = new FileReader();
    const file = e.target.files[0];
    reader.onloadend = () => {
      setUnsaved();
      onChangeProp && onChangeProp(e);
      setUrl(reader.result);
      setFile(file);
    };
    reader.readAsDataURL(file);
  };

  const initiateNewContact = useCallback(async () => {
    if (!initateContact) {
      setLoading(false);
      return;
    }
    // Set default company set for main company selection
    try {
      const companySet = await getDefaultCompanySet();
      if (!companySet) return;

      let mainPartner;
      let isNewCompany = false;

      try {
        // Set existing or new main company based on email address details
        const companyDetails = await getCompanyDetails(emailAddress?.address);

        if (companyDetails) {
          const { name, description, url } = companyDetails;

          const { status, data } = await rest.search("com.axelor.apps.base.db.Partner", {
            fields: ["name", "fullName", "id", "version"],
            data: {
              criteria: [
                {
                  fieldName: "fullName",
                  value: name,
                  operator: "like",
                },
                {
                  fieldName: "companySet.id",
                  value: companySet.map(({ id }) => id),
                  operator: "IN",
                },
                ...MainCompanyCriteria,
              ],
              operator: "and",
            },
          });

          if (status === 0 && Array.isArray(data) && data[0]) {
            mainPartner = data[0];
          } else {
            isNewCompany = true;
            mainPartner = {
              partnerTypeSelect: 1,
              name,
              webSite: url,
              description,
            };
          }
        }
      } catch (err) {
        console.error(err);
      }

      setUnsaved();
      setContact((contact) => ({ ...contact, isNewCompany, companySet, mainPartner: { ...mainPartner } }));
    } finally {
      setLoading(false);
    }
  }, [emailAddress, companySet, initateContact]);

  useEffect(() => {
    updateUnsaved(unsavedProp);
  }, [unsavedProp]);

  useEffect(() => {
    const ids = companySet && companySet.map((company) => company.id);
    setCriteriaIds(ids);
  }, [companySet]);

  useEffect(() => {
    if (id) {
      const timer = setTimeout(() => {
        setLoading(false); // stimulate loading to avoid flash on redirection from list
      }, 200);
      return () => clearTimeout(timer);
    } else {
      initiateNewContact();
    }
  }, []);

  if (loading) {
    return <LoadingView position="fixed" />;
  }

  return (
    <Box display="flex" flexDirection="column" pr={2} pl={2}>
      <Grid className={classes.container}>
        {isBack && (
          <Grid>
            <IconButton onClick={handleRedirection} className={classes.icon}>
              <ArrowBack className={classes.iconButton} />
            </IconButton>
          </Grid>
        )}
        <Grid className={classes.toolbarEnd}>
          {contact && contact.id && (
            <IconButton disabled={saving} onClick={openConfirmation} className={classes.icon}>
              <Delete className={classes.iconButton} />
            </IconButton>
          )}
          <IconButton disabled={saving} onClick={onSave} className={classes.icon}>
            <Save className={classes.iconButton} />
          </IconButton>
        </Grid>
      </Grid>
      <Grid className={classes.main}>
        <Grid className={classes.firstDetailsBlock}>
          <Grid className={classes.imageWrapper}>
            <ImageUpload url={imageUrl} name={name} onChange={uploadImage} />
          </Grid>
          <StaticSelect
            options={[
              { value: 1, label: t("M.") },
              { value: 2, label: t("Ms.") },
              { value: 3, label: t("Dr") },
              { value: 4, label: t("Prof.") },
            ]}
            label={t("Civility")}
            value={titleSelect || ""}
            name="titleSelect"
            handleChange={handleChange}
          />
          <Input placeholder={t("Name")} value={name || ""} handleChange={handleChange} name="name" />
          <Input placeholder={t("First Name")} value={firstName || ""} name="firstName" handleChange={handleChange} />
          {!isNewCompany && (
            <SelectComponent
              model="com.axelor.apps.base.db.Partner"
              title={t("Main Company")}
              value={mainPartner}
              optionLabel="fullName"
              name="mainPartner"
              data={contact}
              update={setContact}
              criteria={MainCompanyCriteria}
              criteriaIds={criteriaIds}
              criteriaFieldName="companySet.id"
              criteriaOperator="IN"
              operator="and"
            />
          )}
          <Box
            mt={1}
            component={FormControlLabel}
            control={<Checkbox checked={isNewCompany} onChange={handleChange} name="isNewCompany" />}
            label={t("New Company")}
          />
          {isNewCompany && (
            <>
              <Input
                placeholder={t("Company Name")}
                value={mainPartner?.name || ""}
                name="mainPartner.name"
                handleChange={handleChange}
                required
              />
              <Input
                placeholder={t("Website")}
                value={mainPartner?.webSite || ""}
                name="mainPartner.webSite"
                handleChange={handleChange}
              />
              <Input
                placeholder={t("Description")}
                value={mainPartner?.description || ""}
                name="mainPartner.description"
                handleChange={handleChange}
              />
            </>
          )}
        </Grid>
        <Tabs
          className={classes.tab}
          tabs={[
            {
              id: 0,
              label: t("Contact"),
              children: (
                <ContactTab
                  unsaved={unsavedRef?.current}
                  contact={contact}
                  handleChange={handleChange}
                  setContact={setContact}
                  search={search}
                  translate={t}
                />
              ),
            },
            {
              id: 1,
              label: t("Others"),
              children: <OthersTab contact={contact} handleChange={handleChange} setContact={setContact} />,
            },
            contact &&
              contact.id && {
                id: 2,
                label: t("Events"),
                className: classes.eventTab,
                children: <EventTab eventFormRef={eventFormRef} contact={contact} handleChange={handleChange} />,
              },
          ]}
        />
      </Grid>
      <Confirmation
        open={isConfirmationOpen}
        title={t("Delete")}
        content={t("Are you sure you want to delete record?")}
        onConfirm={onDelete}
        onClose={closeConfirmation}
        translate={t}
      />
      <Confirmation
        open={isUnsavedConfirmationOpen}
        title={t("Unsaved changes")}
        content={t("There are unsaved changes, are you sure you want to redirect?")}
        onConfirm={redirect}
        onClose={closeUnsavedConfirmation}
        translate={t}
      />
    </Box>
  );
}
