import React from "react";

import { Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

import { Input, Select as SelectComponent } from "../../../components";
import { useTranslation } from "../../../services/translate";

const useStyles = makeStyles(() => ({
  container: {
    display: "flex",
    flexDirection: "column",
  },
}));

export default function OthersTab({ contact, handleChange, setContact, disabled }) {
  const classes = useStyles();
  const { t } = useTranslation();

  const { user, team, companySet, description } = contact;
  return (
    <Grid className={classes.container}>
      <SelectComponent
        title={t("Assigned To")}
        model="com.axelor.auth.db.User"
        value={user}
        optionLabel="fullName"
        name="user"
        data={contact}
        update={setContact}
        disabled={disabled}
      />
      <SelectComponent
        title={t("Team")}
        model="com.axelor.team.db.Team"
        value={team}
        name="team"
        data={contact}
        update={setContact}
        disabled={disabled}
      />
      <SelectComponent
        title={t("Companies associated to")}
        model="com.axelor.apps.base.db.Company"
        multiple={true}
        name="companySet"
        data={contact}
        update={setContact}
        value={companySet}
        disabled={disabled}
        type="multiple"
      />
      <Input
        placeholder={t("Notes")}
        name="description"
        handleChange={handleChange}
        multiline
        disabled={disabled}
        value={description || ""}
      />
    </Grid>
  );
}
