import React, { useState, useEffect, useCallback } from "react";
import moment from "moment";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import { makeStyles } from "@material-ui/core/styles";

import { EventList, EventForm, LoadingView, Confirmation } from "../../../components";
import { useStore } from "../../../services/store";
import rest from "../../../services/rest";
import { useToast } from "../../../services/toast";
import useUnsaved from "../../../hooks/use-unsaved";
import { fetchMainPartner } from "../../events/event-form";
import { useTranslation } from "../../../services/translate";

const useStyles = makeStyles(() => ({
  container: {
    display: "flex",
    flexDirection: "column",
  },
}));

function isValidResponse(res) {
  return res && res.status === 0 && Array.isArray(res.data) && res.data.length;
}

async function fetchPartner({ email }) {
  try {
    const res = await rest.search("com.axelor.apps.base.db.Partner", {
      fields: ["id", "version", "name", "fullName"],
      data: {
        criteria: [
          {
            fieldName: "emailAddress.address",
            value: email,
            operator: "=",
          },
          {
            fieldName: "isContact",
            value: true,
            operator: "=",
          },
        ],
        operator: "and",
      },
    });
    if (isValidResponse(res)) {
      const partner = res.data[0];
      return partner;
    }
  } catch (err) {
    console.log(err);
  }
}

function fetchEvents({ lead }) {
  return rest
    .search("com.axelor.apps.crm.db.Event", {
      fields: [
        "user",
        "partner",
        "lead",
        "subject",
        "startDateTime",
        "endDateTime",
        "subject",
        "statusSelect",
        "typeSelect",
        "id",
        "version",
      ],
      sortBy: ["subject"],
      data: {
        _domain: "self.office365Id != null and self.lead.id = :leadId",
        _domainContext: {
          leadId: lead.id,
        },
      },
    })
    .then(({ status, data } = {}) => {
      return status === 0 && Array.isArray(data) && data;
    });
}

const VIEWS = {
  list: 0,
  form: 1,
};

function EventListContainer({ lead, onClick, onCreateEvent, translate }) {
  const classes = useStyles();

  const { state, update } = useStore();
  const { showDetails } = state;
  const [events, setEvents] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    setLoading(true);

    fetchEvents({ lead })
      .then((events) => {
        if (events) {
          setEvents(events);
          update((draft) => {
            draft.isLoggedIn = "true";
          });
        }
      })
      .finally(() => {
        setLoading(false);
      });
  }, [setEvents, showDetails, update]);

  if (loading) {
    return <LoadingView position="fixed" />;
  }

  return (
    <Grid className={classes.container}>
      <Box p={1} display="flex" flexDirection="column">
        <Button onClick={onCreateEvent}>{translate("Create Event")}</Button>
      </Box>
      <EventList events={events} onClick={onClick} translate={translate} />
    </Grid>
  );
}

function EventFormContainer({ event: eventProp, lead, onBack, onSave, formRef, translate }) {
  const [event, setEvent] = useState({
    typeSelect: "",
    statusSelect: "",
    subject: "",
    ...eventProp,
  });

  const [saving, setSaving] = useState(false);

  const { emailAddress } = lead;

  const { state, update } = useStore();
  const { toastSuccess, toastError } = useToast();

  const { unsavedRef, isUnsavedConfirmationOpen, openUnsavedConfirmation, closeUnsavedConfirmation, setUnsaved } =
    useUnsaved();

  const handleChange = useCallback(
    (e) => {
      const { name, value } = e.target;
      setUnsaved();
      setEvent({
        ...event,
        [name]: value,
      });

      if (name === "contactPartner") {
        fetchMainPartner({ contact: value }).then((partner) => {
          if (partner) {
            setEvent((event) => ({ ...event, partner }));
          }
        });
      }
    },
    [event, setUnsaved]
  );

  const handleDelete = useCallback(
    async (event) => {
      const res = await rest.delete("com.axelor.apps.crm.db.Event", event.id);
      if (res && res.status === 0) {
        toastSuccess(translate("Deleted Successfully"));
        onBack();
      } else {
        toastError(res.data.message);
      }
    },
    [onBack]
  );

  const handleSave = async (event) => {
    if (!event.subject) {
      toastError(translate("Subject is required"));
      return;
    }

    if (!(event.startDateTime && event.startDateTime)) {
      toastError(translate("Start date time & End date time is required"));
      return;
    }

    const eventObj = {
      ...event,
      office365Id: state.office365Id,
    };

    setSaving(true);

    update((draft) => {
      draft.loading = true;
    });

    try {
      const res =
        eventObj && eventObj.id
          ? await rest.update(
              "com.axelor.apps.crm.db.Event",
              {
                ...eventObj,
              },
              eventObj.id
            )
          : await rest.add("com.axelor.apps.crm.db.Event", {
              ...eventObj,
            });
      if (!res) {
        toastError(translate("Error occured! Please try again."));
        return;
      }
      setEvent({
        ...eventObj,
        version: res.data[0].version,
      });
      toastSuccess(translate("Event saved Successfully"));
      update((draft) => {
        draft.loading = false;
      });
      onSave && onSave();
      return true;
    } catch (err) {
    } finally {
      setSaving(false);
    }
  };

  const handleBack = useCallback(() => {
    if (unsavedRef.current) {
      openUnsavedConfirmation();
    } else {
      onBack();
    }
  }, [onBack, openUnsavedConfirmation, unsavedRef]);

  useEffect(() => {
    const email = emailAddress && emailAddress.address;
    if (!email) return;

    fetchPartner({ email }).then((partner) => {
      if (partner) {
        setEvent((event) => ({ ...event, partner }));
      }
    });
  }, [emailAddress]);

  return (
    <>
      <EventForm
        formRef={formRef}
        event={event}
        onChange={handleChange}
        onDelete={handleDelete}
        onBack={handleBack}
        onSave={handleSave}
        saving={saving}
        translate={translate}
        hideLead
      />
      <Confirmation
        open={isUnsavedConfirmationOpen}
        title={translate("Unsaved changes")}
        content={translate("There are unsaved changes, are you sure you want to redirect?")}
        onConfirm={onBack}
        onClose={closeUnsavedConfirmation}
        translate={translate}
      />
    </>
  );
}

export default function EventsTab({ lead, eventFormRef }) {
  const [view, setView] = useState(VIEWS.list);
  const [event, setEvent] = useState(null);
  const { state } = useStore();
  const { office365Id } = state;
  const { t } = useTranslation();

  const handleClick = useCallback((event) => {
    setEvent({
      ...event,
      startDateTime: moment(event.startDateTime).format("YYYY-MM-DDThh:mm"),
      endDateTime: moment(event.endDateTime).format("YYYY-MM-DDThh:mm"),
    });
    setView(VIEWS.form);
  }, []);

  const handleCreateEvent = useCallback(() => {
    setEvent({
      typeSelect: "0",
      statusSelect: 1,
      office365Id,
      lead: { id: lead.id, name: lead.name, fullName: lead.name || lead.fullName },
    });
    setView(VIEWS.form);
  }, []);

  const handleSave = useCallback(() => {
    setView(VIEWS.list);
    setEvent(null);
  }, []);

  const handleFormBack = useCallback(() => {
    setView(VIEWS.list);
    setEvent(null);
  }, []);

  return (
    <>
      {view === VIEWS.list && (
        <EventListContainer onCreateEvent={handleCreateEvent} onClick={handleClick} lead={lead} translate={t} />
      )}
      {view === VIEWS.form && (
        <EventFormContainer
          formRef={eventFormRef}
          onSave={handleSave}
          onBack={handleFormBack}
          lead={lead}
          event={event}
          translate={t}
        />
      )}
    </>
  );
}
