import React, { useState, useCallback, useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import IconButton from "@material-ui/core/IconButton";
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";
import moment from "moment";

import AddIcon from "@material-ui/icons/Add";
import DeleteIcon from "@material-ui/icons/Delete";

import { Select as SelectComponent, StaticSelect, LoadingView, Input } from "../components";
import rest from "../services/rest";
import { useToast } from "../services/toast";
import { useTranslation } from "../services/translate";

function handleSearchResponse(res) {
  return res && res.status === 0 && Array.isArray(res.data) && res.data.length && res.data[0];
}

function formatDateTime(timestamp) {
  return moment(timestamp).format("yyyy-MM-DDTHH:mm:ss");
}

function formatEmail({ emailAddress, displayName }) {
  return {
    emailAddress: {
      address: emailAddress,
      name: displayName,
    },
  };
}

function fetchEWSToken() {
  return new Promise((resolve) => {
    Office.context.mailbox.getCallbackTokenAsync(function (result) {
      if (result.status === Office.AsyncResultStatus.Succeeded) {
        resolve(result.value);
      } else {
        resolve();
      }
    });
  });
}

const nameColumnCache = {};

async function fetchModelNameColumn(model) {
  if (nameColumnCache[model]) return nameColumnCache[model];

  const nameColumn = await rest
    .get(`/ws/meta/fields/${model}`)
    .then((res) => res && res.status === 0 && res.data && Array.isArray(res.data.fields) && res.data.fields)
    .then((fields = []) => fields.find((item) => item.nameColumn))
    .then((field = {}) => field.name || "name");

  nameColumnCache[model] = nameColumn;

  return nameColumn;
}

const commentModel = "com.axelor.mail.db.MailMessage";

async function fetchComments(options) {
  const { relatedModel, relatedId, subject } = options;

  return rest
    .search(commentModel, {
      fields: ["id", "body"],
      data: {
        _domain: "self.relatedModel = :relatedModel and self.relatedId = :relatedId and self.subject = :subject",
        _domainContext: {
          relatedModel,
          relatedId,
          subject,
        },
      },
    })
    .then((res) => {
      if (res && res.status === 0 && Array.isArray(res.data) && res.data.length) {
        return res.data;
      }
      return [];
    });
}

async function fetchMail() {
  const { item } = Office?.context?.mailbox || {};

  if (!item) return;

  const { itemId: id } = item;
  const mailModel = "com.axelor.message.db.Message";

  let mail = await rest
    .search(mailModel, {
      fields: ["id", "multiRelatedList"],
      data: {
        _domain: "self.messageId = :id",
        _domainContext: {
          id,
        },
      },
    })
    .then(handleSearchResponse);

  if (!mail) return;

  mail = await rest
    .fetchId(mailModel, mail.id, {
      fields: ["subject", "multiRelatedList"],
      related: {
        multiRelatedList: ["relatedToSelectId", "relatedToSelect"],
      },
    })
    .then(handleSearchResponse);

  if (!mail) return;

  const relatedTo = await Promise.all(
    mail.multiRelatedList.map(async ({ relatedToSelect, relatedToSelectId }) => {
      const nameColumn = await fetchModelNameColumn(relatedToSelect);
      return {
        relatedTo: relatedToSelect,
        relatedToSelectId: await rest
          .fetchId(relatedToSelect, relatedToSelectId, {
            fields: [nameColumn, "name"],
          })
          .then(handleSearchResponse),
        comments: await fetchComments({
          relatedModel: relatedToSelect,
          relatedId: relatedToSelectId,
          subject: mail.subject,
        }),
      };
    })
  );

  mail.relatedTo = relatedTo;

  return mail;
}

function generateMail() {
  const { item, ewsUrl } = Office.context.mailbox;
  if (item) {
    return new Promise((resolve) => {
      Office.context.mailbox?.item?.body?.getAsync(Office.CoercionType.Html, async (body) => {
        const { itemId: id, subject, dateTimeCreated, from, to, cc, bcc, attachments } = item;
        const { value: bodyPreview } = body;

        const userEmail = Office.context.mailbox.userProfile.emailAddress;
        const isRead = from.emailAddress !== userEmail;

        const mail = {
          id,
          subject,
          bodyPreview,
          sentDateTime: formatDateTime(dateTimeCreated),
          receivedDateTime: formatDateTime(dateTimeCreated),
          isDraft: false,
          isRead,
          from: formatEmail(from),
          toRecipients: to.map(formatEmail),
          replyTo: [],
          ccRecipients: cc.map(formatEmail),
          bccRecipients: bcc.map(formatEmail),
          attachments,
        };

        if (attachments && Array.isArray(attachments) && attachments.length) {
          mail.ewsUrl = ewsUrl;
          mail.ewsToken = await fetchEWSToken();
        }

        resolve(mail);
      });
    });
  } else {
    return;
  }
}

function createMessage(mailObject) {
  return rest.action({
    action: "action-office365-method-create-mail",
    model: "com.axelor.message.db.Message",
    data: {
      context: {
        data: { ...mailObject },
      },
    },
  });
}

async function fetchModelOptions() {
  const options = await rest
    .search("com.axelor.meta.db.MetaSelectItem", {
      fields: ["id", "title", "value"],
      data: {
        _domain: "self.select.name like :name",
        _domainContext: {
          name: "message.related.to.select",
        },
        _archived: true,
      },
    })
    .then((res) => res.status === 0 && Array.isArray(res.data) && res.data)
    .then((options) => options || [])
    .then((options) => options.map(({ title: label, ...rest }) => ({ ...rest, label })));

  const modelShowCommentMap = await rest
    .action({
      action: "action-office365-method-get-tracking-meta-model",
      model: "com.axelor.meta.db.MetaSelectItem",
    })
    .then(({ data }) => data)
    .then((models = []) =>
      models.reduce((acc, item) => {
        acc[item.value] = item.isTracking;
        return acc;
      }, {})
    );

  return Promise.all(options.map((option) => fetchModelNameColumn(option.value))).then((nameColumns) => {
    return options.map((option, index) => ({
      ...option,
      optionLabel: nameColumns[index],
      fields: [nameColumns[index]],
      showComment: modelShowCommentMap[option.value],
    }));
  });
}

const getOption = (data, options) => {
  return data.relatedTo && options.find((o) => o.value === data.relatedTo);
};

const getOptionFields = (data, options) => {
  const option = getOption(data, options);
  return option ? (option || { fields: [] }).fields || [] : [];
};

const getOptionLabel = (data, options) => {
  const option = getOption(data, options);
  return option ? (option || { optionLabel: "name" }).optionLabel || "name" : "name";
};

const useRelatedRowStyles = makeStyles((theme) => ({
  select: {
    "& .MuiOutlinedInput-root": {
      height: theme.spacing(7),
    },
  },
  shadow: {
    boxShadow: "0px 3px 15px 0 rgba(0, 0, 0, 0.11)",
  },
  comment: {
    marginBottom: 0,
  },
}));

function RelatedRow({ data, onChange, onDelete, disabled, modelOptions }) {
  const classes = useRelatedRowStyles();
  const { t } = useTranslation();

  const setData = useCallback(
    (e) => {
      onChange({ ...data, ...e });
    },
    [data, onChange]
  );

  const { comments = [] } = data;

  const modelOption = modelOptions.find((option) => option.value === data.relatedTo);

  return (
    <Box display="flex" flexDirection="column" className={classes.shadow} p={1}>
      <Box display="flex" alignItems="center" justifyContent="space-between" mb={1}>
        <Typography>
          <b>{t("Related To")}</b>
        </Typography>
        <IconButton onClick={onDelete} disabled={disabled}>
          <DeleteIcon />
        </IconButton>
      </Box>
      <StaticSelect
        options={modelOptions}
        label={t("Model")}
        value={data.relatedTo || ""}
        name="relatedTo"
        handleChange={({ target: { name, value } }) => {
          setData({ [name]: value, relatedToSelectId: "" });
        }}
        displayEmpty
        className={classes.select}
        disabled={disabled}
      />
      <SelectComponent
        title={t("Record")}
        index={data.relatedTo}
        model={data.relatedTo || ""}
        optionLabel={getOptionLabel(data, modelOptions)}
        value={data.relatedToSelectId}
        name="relatedToSelectId"
        update={setData}
        fields={getOptionFields(data, modelOptions)}
        className={classes.select}
        disabled={disabled}
      />
      {modelOption && modelOption.showComment && (
        <>
          <Box mb={2} display="flex" alignItems="center" justifyContent="flex-end">
            <Button
              disabled={disabled}
              onClick={() => {
                setData({ comments: [...data.comments, {}] });
              }}
            >
              {t("Add comment")}
            </Button>
          </Box>
          {comments.map((comment, index) => {
            return (
              <Box mb={1} display="flex" alignItems="center" justifyContent="space-between" key={index}>
                <Input
                  name="comments"
                  label={t("Comment")}
                  placeholder={t("Comment")}
                  className={classes.comment}
                  value={(comment && comment.body) || ""}
                  handleChange={({ target: { name, value } }) => {
                    setData({
                      [name]: data[name].map((item, _index) =>
                        index === _index ? { ...data[name][index], body: value } : item
                      ),
                    });
                  }}
                  disabled={disabled}
                />
                <IconButton
                  onClick={() => {
                    setData({ comments: data.comments.filter((_, i) => i !== index) });
                  }}
                  disabled={disabled}
                >
                  <DeleteIcon />
                </IconButton>
              </Box>
            );
          })}
        </>
      )}
    </Box>
  );
}

const useStyles = makeStyles(() => ({
  iconButton: {
    color: "#000000",
  },
}));

function CopyEmail() {
  const classes = useStyles();

  const { toastSuccess, toastError } = useToast();

  const [saving, setSaving] = useState(false);
  const [existingMail, setExistingMail] = useState(false);
  const [relatedSelectList, setRelatedSelectList] = useState([{ comments: [] }]);
  const [loading, setLoading] = useState(false);
  const [modelOptions, setModelOptions] = useState([]);
  const { t } = useTranslation();

  const handleAdd = useCallback(() => {
    setRelatedSelectList((list) => {
      return [...list, { comments: [{}] }];
    });
  }, []);

  const handleChange = useCallback((record, index) => {
    setRelatedSelectList((list) => {
      const newList = [...list];
      newList[index] = { ...record };
      return [...newList];
    });
  }, []);

  const handleDelete = useCallback((index) => {
    setRelatedSelectList((list) => {
      const newList = [...list];
      newList.splice(index, 1);
      return newList;
    });
  }, []);

  const initiate = useCallback(() => {
    setLoading(true);
    Promise.all([fetchMail(), fetchModelOptions()])
      .then(([mail, modelOptions]) => {
        if (mail) {
          setExistingMail(mail);
          mail?.relatedTo?.length && setRelatedSelectList(mail.relatedTo);
        }

        if (modelOptions) {
          setModelOptions(modelOptions);
        }
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        setLoading(false);
      });
  }, []);

  const handleSendEmail = useCallback(async () => {
    setSaving(true);

    const mail = await generateMail();

    if (!mail) {
      toastError(t("Email copying failed. Please try again"));
      setSaving(false);
      return;
    }

    try {
      const list = relatedSelectList.map((item) => ({ ...item, relatedToSelectId: (item.relatedToSelectId || {}).id }));

      const { status } = await createMessage({ ...mail, relatedSelectList: list });

      if (status !== 0) {
        toastError(t("Email copying failed. Please try again"));
        return;
      }

      setExistingMail(true);
      toastSuccess(t("Email copied successfully"));
      initiate();
    } catch (err) {
      toastError(t("Email copying failed. Please try again"));
    } finally {
      setSaving(false);
    }
  }, [createMessage, relatedSelectList, toastSuccess, toastError, initiate]);

  useEffect(() => {
    initiate();
  }, []);

  if (loading) {
    return <LoadingView />;
  }

  return (
    <Box display="flex" pr={2} pl={2}>
      <Box width="100%">
        <Box mt={2} mb={2}>
          <Button onClick={handleSendEmail} disabled={saving}>
            {existingMail ? t("Update") : t("Copy")}
          </Button>
        </Box>
        {relatedSelectList.map((relatedItem, i) => (
          <RelatedRow
            key={i}
            data={relatedItem}
            onChange={(record) => handleChange(record, i)}
            onDelete={() => handleDelete(i)}
            disabled={saving}
            modelOptions={modelOptions}
          />
        ))}
        <Box mt={1}>
          <IconButton onClick={handleAdd} className={classes.iconButton} disabled={saving}>
            <AddIcon />
          </IconButton>
        </Box>
      </Box>
    </Box>
  );
}

export default CopyEmail;
