import { redirect } from "./utils";
import { getServerURL, setServerURL, getXCSRF, removeXCSRF, setXCSRF, setLoggedIn } from "./storage";

export class Service {
  constructor() {
    const headers = {
      Accept: "application/json",
      "content-type": "application/json",
      "X-Requested-With": "XMLHttpRequest",
    };
    this.baseURL = getServerURL();
    this.headers = headers;
  }

  fetch(url, method, options) {
    if (url.includes("null/")) {
      setLoggedIn(false);
      redirect();
      return Promise.resolve({ data: { err: "error", status: -1 } });
    }
    return fetch(url, options)
      .then((data) => {
        if (data.status === 401 || !this.baseURL) {
          setLoggedIn(false);
          redirect();
          return Promise.reject({ data: { err: "error", status: data.status } });
        } else {
          if (url.endsWith("/login.jsp") || ["head"].indexOf(method.toLowerCase()) !== -1) {
            return data;
          }
          const contentType = data.headers.get("content-type");
          let isJSON = contentType ? data.headers.get("content-type").includes("application/json") : false;
          return isJSON ? data.json() : data;
        }
      })
      .catch((err) => {
        setLoggedIn(false);
        redirect();
        throw err;
      });
  }

  request(url, config = {}, data = {}) {
    const xcsrf = getXCSRF();
    const headers = { ...this.headers };
    if (xcsrf && !url.endsWith("/callback")) {
      headers["X-CSRF-Token"] = xcsrf;
    }
    const options = Object.assign(
      {
        method: "POST",
        credentials: "include",
        headers,
        mode: "cors",
        body: JSON.stringify(data),
      },
      config
    );

    if (config.method === "GET") {
      delete options.body;
    }
    return this.fetch(`${this.baseURL}${url.indexOf("/") === 0 ? url : `/${url}`}`, config.method, options);
  }

  get(url) {
    const config = {
      method: "GET",
    };

    return this.request(url, config);
  }

  post(url, data) {
    const config = {
      method: "POST",
    };
    return this.request(url, config, data).catch((res) => {
      console.error("Failed to connect");
    });
  }

  put(url, data) {
    const config = {
      method: "PUT",
    };
    return this.request(url, config, data);
  }

  add(entity, record) {
    const data = {
      data: record,
    };
    const url = `ws/rest/${entity}`;
    return this.post(url, data);
  }

  search(entity, options) {
    const data = {
      offset: 0,
      ...options,
    };
    const url = `ws/rest/${entity}/search`;
    return this.post(url, data);
  }

  fetchId(entity, id, data = {}) {
    const url = `ws/rest/${entity}/${id}/fetch`;
    return this.post(url, data);
  }

  action(data) {
    const url = `ws/action`;
    return this.post(url, data);
  }

  update(entity, record, id) {
    const data = {
      data: record,
    };
    const url = `ws/rest/${entity}/${id}`;
    return this.post(url, data);
  }

  login(inputURL = this.baseURL, data) {
    this.baseURL = inputURL;
    removeXCSRF();
    return this.post("/callback", data)
      .then((res = {}) => {
        if (res.status === 200) {
          const xcsrf = res.headers.get("X-CSRF-Token");
          if (xcsrf) {
            setXCSRF(xcsrf);
          }
          setServerURL(this.baseURL);
          return { loggedIn: true, status: res.status };
        }
        return { loggedIn: false, status: res.status };
      })
      .catch((err) => console.log(err));
  }

  logout() {
    return this.post("/logout");
  }

  delete(entity, id) {
    const config = {
      method: "DELETE",
    };
    const url = `ws/rest/${entity}/${id}`;
    return this.request(url, config);
  }

  info() {
    return this.get("/ws/app/info");
  }

  checkInfo() {
    return this.info()
      .then((info) => info || {})
      .then((info) => ({
        id: info["user.id"],
        code: info["user.login"],
        name: info["user.name"],
        language: info["user.lang"] || "en",
        email: info["user.login"],
        theme: info["user.theme"] || "",
      }));
  }

  uploadFile(data = null, headers = {}, callback = () => true, info = {}) {
    return new Promise((resolve, reject) => {
      const xhr = new XMLHttpRequest(),
        method = "POST",
        url = `${this.baseURL}/ws/files/upload`;

      const doClean = () => Promise.resolve(true);

      const formatSize = (done, total) => {
        const format = (size) => {
          if (size > 1000000000) return parseFloat(size / 1000000000).toFixed(2) + " GB";
          if (size > 1000000) return parseFloat(size / 1000000).toFixed(2) + " MB";
          if (size >= 1000) return parseFloat(size / 1000).toFixed(2) + " KB";
          return size + " B";
        };
        return format(done || 0) + "/" + format(total);
      };

      xhr.open(method, url, true);

      Object.keys(headers).forEach((k) => {
        xhr.setRequestHeader(k, headers[k]);
      });

      xhr.withCredentials = true;
      xhr.overrideMimeType("application/octet-stream");
      xhr.setRequestHeader("Content-Type", "application/octet-stream");
      xhr.setRequestHeader("X-Requested-With", "XMLHttpRequest");
      xhr.setRequestHeader("X-CSRF-Token", getXCSRF());

      xhr.onload = () => {
        callback(100);
      };

      info.abort = () => {
        xhr.abort();
        return doClean();
      };

      xhr.onreadystatechange = function () {
        if (xhr.readyState === 4) {
          switch (xhr.status) {
            case 401:
              throw new Error("Unauthorized");
            case 200:
              try {
                const result = JSON.parse(xhr.responseText);
                resolve({
                  result,
                  url: `${this.baseURL}ws/rest/com.axelor.meta.db.MetaFile/${result.id}/content/download?v=0`,
                });
              } catch (e) {
                resolve(xhr.responseText);
              }
              break;
            default:
              doClean();
              reject({ status: xhr.status });
              break;
          }
        }
      };

      xhr.upload.onprogress = (e) => {
        const fileSize = headers["X-File-Size"];
        const total = parseFloat(headers["X-File-Offset"]) + e.loaded;
        const done = Math.round((total / fileSize) * 100);

        info.progress = done > 95 ? "95%" : done + "%";
        info.transfer = formatSize(total, fileSize);
        info.loaded = total === fileSize;

        if (e.lengthComputable) {
          callback((e.loaded / e.total) * 100, info);
        }
      };
      xhr.send(data);
    });
  }
}

export default new Service();
