import React, { useState, useEffect, useCallback } from "react";
import { goTo } from "route-lite";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";

import { Input } from "../components";
import rest from "../services/rest";
import { getLoggedIn, getServerURL, setLoggedIn } from "../services/storage";
import View from "./view";
import { useStore } from "../services/store";
import Template from "./template";
import { useToast } from "../services/toast";
import { useTranslation } from "../services/translate";

const useStyles = makeStyles(() => ({
  textBlue: {
    color: "#32325d",
  },
  textGreen: {
    color: "#3ecf8e",
  },
  picture: {
    height: 210,
    width: "100%",
    backgroundSize: "contain",
    backgroundRepeat: "no-repeat",
    backgroundPosition: "center",
    backgroundImage: "url(./assets/login.svg)",
  },
}));

const responseMessage = {
  200: "Logged in successfully",
  404: "URL not found",
  500: "Internal server error",
};

export default function Login({ isLoggedIn }) {
  const classes = useStyles();
  const { state, update } = useStore();
  const { isCompose } = state;
  const { toastSuccess, toastError } = useToast();
  const { t } = useTranslation();

  const [details, setDetails] = useState({
    url: getServerURL() || "",
    username: "",
    password: "",
  });

  const handleLogin = useCallback(
    (event) => {
      event.preventDefault();

      let { url, username, password } = details;

      if (!(url && username && password)) {
        toastError(t("URL, Username and Password is required"));
        return;
      }

      update((draft) => {
        draft.loading = true;
      });

      if (url.endsWith("/login.jsp")) {
        url = url.replace("/login.jsp", "");
      }

      rest
        .login(url, {
          username: username,
          password: password,
        })
        .then(({ loggedIn, status } = {}) => {
          update((draft) => {
            draft.loading = false;
          });
          if (loggedIn) {
            setLoggedIn(true);
            toastSuccess(t(responseMessage[status]) || t("Logged in successfully"));
            update((draft) => {
              draft.isLoggedIn = "true";
            });
            goTo(isCompose ? Template : View);
          } else {
            update((draft) => {
              draft.isLoggedIn = "false";
            });

            toastError(t(responseMessage[status]) || t("Invalid credentials"));
          }
        });
    },
    [details, isCompose, update]
  );

  const handleChange = useCallback((event) => {
    const { name, value } = event.target;
    setDetails((details) => ({
      ...details,
      [name]: value,
    }));
  }, []);

  useEffect(() => {
    update((draft) => {
      draft.loading = false;
    });
    if (isLoggedIn) {
      update((draft) => {
        draft.isLoggedIn = getLoggedIn();
      });
    }
  }, [isLoggedIn, update]);

  return (
    <>
      <Box onSubmit={handleLogin} component="form" pl={2} pr={2} display="flex" flexDirection="column">
        <Box display="flex" alignItems="center" justifyContent="center" pt={2} pb={2}>
          <Typography variant="h5" className={classes.textBlue}>
            <b>{t("Axelor")}</b>
          </Typography>
          &nbsp;
          <Typography variant="h5" className={classes.textGreen}>
            <b>{t("Open Suite")}</b>
          </Typography>
        </Box>
        <Typography className={classes.textBlue} align="center">
          <b>{t("Modular and intelligent platform of business management")}</b>
        </Typography>
        <div className={classes.picture} />
        <Box display="flex" flexDirection="column" justifyContent="center" alignItems="center" mt={1}>
          <Input fullWidth name="url" placeholder="URL" value={details.url} handleChange={handleChange} />
          <Input
            fullWidth
            name="username"
            placeholder={t("Username")}
            value={details.username}
            handleChange={handleChange}
          />
          <Input
            fullWidth
            name="password"
            type="password"
            placeholder={t("Password")}
            value={details.password}
            handleChange={handleChange}
          />
        </Box>
        <Button type="submit">{t("Login")}</Button>
      </Box>
    </>
  );
}
