import React from "react";

import { Grid, Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

import { Input, Select as SelectComponent } from "../../../components";
import { useTranslation } from "../../../services/translate";

const useStyles = makeStyles(() => ({
  root: {
    display: "flex",
    flexDirection: "column",
  },
  container: {
    display: "flex",
    flexDirection: "column",
    boxShadow: "0px 3px 15px 0 rgba(0, 0, 0, 0.11)",
    marginTop: 15,
    padding: 24,
  },
  typography: {
    textAlign: "left",
    marginBottom: 25,
  },
}));

export default function OthersTab({ lead, handleChange, setLead, disabled }) {
  const classes = useStyles();

  const { t } = useTranslation();

  const {
    jobTitleFunction,
    enterpriseName,
    industrySector,
    source,
    primaryAddress,
    primaryCity,
    primaryCountry,
    primaryPostalCode,
    primaryState,
    description,
    team,
  } = lead;
  return (
    <Grid className={classes.root}>
      <Grid className={classes.container}>
        <SelectComponent
          title={t("Function")}
          model="com.axelor.apps.base.db.Function"
          value={jobTitleFunction}
          name="jobTitleFunction"
          data={lead}
          update={setLead}
          disabled={disabled}
        />
        <Input
          placeholder={t("Enterprise name")}
          value={enterpriseName || ""}
          name="enterpriseName"
          handleChange={handleChange}
          disabled={disabled}
        />
        <SelectComponent
          title={t("Industry sector")}
          model="com.axelor.apps.base.db.IndustrySector"
          value={industrySector}
          optionLabel="name"
          name="industrySector"
          data={lead}
          update={setLead}
          disabled={disabled}
        />
        <SelectComponent
          title={t("Source")}
          model="com.axelor.apps.base.db.Source"
          value={source}
          optionLabel="name"
          name="source"
          data={lead}
          update={setLead}
          disabled={disabled}
        />
      </Grid>
      <Grid className={classes.container}>
        <Typography variant="h6" className={classes.typography}>
          {t("Primary Address")}
        </Typography>
        <Input
          placeholder={t("Address")}
          name="primaryAddress"
          handleChange={handleChange}
          disabled={disabled}
          value={primaryAddress || ""}
        />

        <SelectComponent
          title={t("City")}
          model="com.axelor.apps.base.db.City"
          value={primaryCity}
          name="primaryCity"
          data={lead}
          update={setLead}
          disabled={disabled}
        />
        <SelectComponent
          title={t("Region")}
          model="com.axelor.apps.base.db.Region"
          value={primaryState}
          name="primaryState"
          data={lead}
          update={setLead}
          disabled={disabled}
        />
        <Input
          placeholder={t("PostalCode")}
          name="primaryPostalCode"
          handleChange={handleChange}
          disabled={disabled}
          value={primaryPostalCode || ""}
        />
        <SelectComponent
          title={t("Country")}
          model="com.axelor.apps.base.db.Country"
          value={primaryCountry}
          name="primaryCountry"
          data={lead}
          update={setLead}
          disabled={disabled}
        />
      </Grid>
      <Grid className={classes.container}>
        <Input
          placeholder={t("Description")}
          name="description"
          handleChange={handleChange}
          multiline
          disabled={disabled}
          value={description || ""}
        />
        <SelectComponent
          title={t("Team")}
          model="com.axelor.team.db.Team"
          value={team}
          name="team"
          data={lead}
          update={setLead}
          disabled={disabled}
        />
      </Grid>
    </Grid>
  );
}
