import { useRef } from "react";
import useConfirmation from "./use-confirmation";

export function useUnsaved({ initialValue = false } = {}) {
  const unsavedRef = useRef(initialValue);

  const {
    isConfirmationOpen: isUnsavedConfirmationOpen,
    openConfirmation: openUnsavedConfirmation,
    closeConfirmation: closeUnsavedConfirmation,
  } = useConfirmation();

  const setUnsaved = () => {
    unsavedRef.current = true;
  };

  const clearUnsaved = () => {
    unsavedRef.current = false;
  };

  const updateUnsaved = (value) => {
    unsavedRef.current = value;
  };

  return {
    unsavedRef,
    isUnsavedConfirmationOpen,
    openUnsavedConfirmation,
    closeUnsavedConfirmation,
    setUnsaved,
    clearUnsaved,
    updateUnsaved,
  };
}

export default useUnsaved;
