import React, { useCallback, useEffect, useRef, useState } from "react";
import Box from "@material-ui/core/Box";
import List from "@material-ui/core/List";
import ListItemText from "@material-ui/core/ListItemText";
import { makeStyles } from "@material-ui/core/styles";
import { goTo } from "route-lite";

import Details from "../details";
import leadForm from "./lead-form";
import rest from "../../services/rest";
import { ListItem, LoadingView, SearchInput } from "../../components";
import { useStore } from "../../services/store";
import { useAddButton } from "../../hooks";
import { useTranslation } from "../../services/translate";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    backgroundColor: theme.palette.background.paper,
  },
}));

function fetchLeads(search) {
  return rest
    .search("com.axelor.apps.crm.db.Lead", {
      fields: [
        "enterpriseName",
        "industrySector",
        "source",
        "primaryAddress",
        "primaryCity",
        "primaryCountry",
        "primaryPostalCode",
        "primaryState",
        "statusSelect",
        "emailAddress",
        "emailAddress.address",
        "name",
        "firstName",
        "titleSelect",
        "fixedPhone",
        "jobTitleFunction",
        "mobilePhone",
        "user",
        "team",
        "description",
        "picture",
        "webSite",
        "id",
        "version",
      ],
      sortBy: ["firstName"],
      data: {
        _domain:
          "lower(self.emailAddress.address) like :search or lower(self.name) like :search or lower(self.firstName) like :search",
        _domainContext: {
          search: `%${search.toLowerCase()}%`,
        },
      },
    })
    .then(({ data, status } = {}) => {
      return status === 0 && Array.isArray(data) ? data : [];
    });
}

export default function Leads({ search }) {
  const classes = useStyles();
  const { state, update } = useStore();
  const { showDetails } = state;
  const [leads, setLeads] = useState([]);
  const [loading, setLoading] = useState(false);
  const searchRef = useRef("");
  const { t } = useTranslation();

  const handleFetchLeads = useCallback(
    (search = "") => {
      searchRef.current = search;
      setLoading(true);

      fetchLeads(search)
        .then((leads) => {
          leads && setLeads(leads);
          update((draft) => {
            draft.isLoggedIn = "true";
          });
        })
        .finally(() => {
          setLoading(false);
        });
    },
    [update]
  );

  useEffect(() => {
    if (showDetails) {
      goTo(Details);
    }

    handleFetchLeads(search);
  }, [showDetails, handleFetchLeads]);

  useAddButton();

  return (
    <>
      {loading && <LoadingView position="fixed" />}
      <SearchInput onChange={handleFetchLeads} initialValue={search} translate={t} />
      <Box display="flex">
        <List className={classes.root}>
          {leads && Boolean(leads.length) ? (
            leads.map((lead, i) => (
              <React.Fragment key={i}>
                <ListItem
                  subtitle={lead.enterpriseName}
                  title={`${lead.firstName || ""} ${lead.name} `}
                  onClick={() => {
                    goTo(leadForm, {
                      lead: {
                        ...lead,
                        emailAddress: {
                          ...lead.emailAddress,
                          address: lead["emailAddress.address"],
                        },
                      },
                      isBack: true,
                      search: searchRef.current,
                    });
                  }}
                  divider
                />
              </React.Fragment>
            ))
          ) : (
            <ListItem>
              <ListItemText primary={t("No leads available")} />
            </ListItem>
          )}
        </List>
      </Box>
    </>
  );
}
