import React from "react";
import clsx from "clsx";
import { TextField } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles({
  input: {
    marginBottom: 15,
  },
  label: {
    color: "black",
    fontSize: 14,
    "&#outlined-basic-label": {
      color: "black",
    },
  },
  outlinedRoot: {
    color: "black",
    "&:hover $notchedOutline": {},
    "&$focused $notchedOutline": {},
  },
  notchedOutline: {},
  focused: {
    color: "black",
  },
  disableInput: {
    color: "black",
  },
});

export function Input(props) {
  const {
    type,
    required,
    disabled,
    placeholder,
    value,
    multiline,
    handleChange,
    name,
    onBlur = () => {},
    label,
    InputLabelProps,
    defaultValue,
    readOnly,
    fullWidth,
    className,
    showLabel = true,
  } = props;

  const classes = useStyles();
  return (
    <TextField
      InputProps={{
        classes: {
          root: classes.outlinedRoot,
          notchedOutline: classes.notchedOutline,
          focused: classes.focused,
          disabled: classes.disableInput,
        },
        readOnly,
      }}
      InputLabelProps={{
        className: classes.label,
        ...InputLabelProps,
      }}
      className={clsx(classes.input, className)}
      multiline={multiline}
      type={type}
      {...(showLabel ? { label: label || placeholder } : {})}
      placeholder={placeholder}
      variant="outlined"
      disabled={disabled}
      required={required}
      value={value}
      defaultValue={defaultValue}
      rows={3}
      onChange={handleChange}
      name={name}
      onBlur={onBlur}
      fullWidth={fullWidth}
    />
  );
}

export default Input;
