import React, { useState, useEffect } from "react";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import Link from "@material-ui/core/Link";
import { makeStyles } from "@material-ui/core/styles";

import { getCompanyDetails } from "./utils";
import { useTranslation } from "../../services/translate";

const useStyles = makeStyles(() => ({
  root: {
    display: "flex",
    alignItems: "flex-start",
    padding: 20,
    flexDirection: "column",
    color: "rgb(50, 50, 93)",
  },
  container: {
    display: "flex",
    alignItems: "flex-start",
    flexDirection: "column",
    marginBottom: 20,
  },
  image: {
    width: 80,
    height: "100%",
    marginBottom: 20,
  },
  title: {
    fontSize: 18,
    fontWeight: "600",
  },
}));

export default function CompanyInformation({ email }) {
  const [details, setDetails] = useState(null);
  const { url, name, image, description, wiki } = details || {};
  const classes = useStyles();
  const { t } = useTranslation();

  useEffect(() => {
    if (!email) return;
    getCompanyDetails(email).then((details) => {
      details && setDetails(details);
    });
  }, [email]);

  return (
    <React.Fragment>
      {details && (
        <Grid className={classes.root}>
          <Typography variant="h5" style={{ marginBottom: 20, fontWeight: "600", color: "#037f51" }}>
            {t("Company Information")}
          </Typography>
          {image && <img src={image} alt={name} className={classes.image} />}
          {name && (
            <Grid className={classes.container}>
              <Typography className={classes.title}>{t("Name")}</Typography>
              <Typography align="left">{name}</Typography>
            </Grid>
          )}
          {url && (
            <Grid className={classes.container}>
              <Typography className={classes.title}>{t("Website")}</Typography>
              <Typography>
                <Link href={url} target="_blank" rel="noopener">
                  {url}
                </Link>
              </Typography>
            </Grid>
          )}
          {description && (
            <Grid className={classes.container}>
              <Typography className={classes.title}>{t("Description")}</Typography>
              <Typography style={{ textAlign: "left" }}>{description}</Typography>
            </Grid>
          )}
          {wiki && (
            <Grid className={classes.container}>
              <Typography className={classes.title}>{t("Other Details")}</Typography>
              <Typography className={classes.title} style={{ fontWeight: 500 }}>
                {t("Wiki")}
              </Typography>
              <Typography>
                <Link href={wiki} target="_blank" rel="noopener">
                  {wiki}
                </Link>
              </Typography>
            </Grid>
          )}
        </Grid>
      )}
    </React.Fragment>
  );
}
