import React, { useCallback, useEffect, useRef, useState } from "react";
import moment from "moment";
import { goTo } from "route-lite";
import Box from "@material-ui/core/Box";

import { LoadingView, EventList, SearchInput } from "../../components";
import rest from "../../services/rest";
import { useStore } from "../../services/store";
import Details from "../details";
import EventForm from "./event-form";
import { useAddButton } from "../../hooks";
import { useTranslation } from "../../services/translate";

function fetchEvents(search) {
  return rest
    .search("com.axelor.apps.crm.db.Event", {
      fields: [
        "user",
        "partner",
        "lead",
        "subject",
        "startDateTime",
        "endDateTime",
        "subject",
        "statusSelect",
        "typeSelect",
        "id",
        "version",
        "contactPartner",
      ],
      sortBy: ["subject"],
      data: {
        _domain: "self.office365Id != null and lower(self.subject) like :search",
        _domainContext: {
          search: `%${search.toLowerCase()}%`,
        },
      },
    })
    .then(({ status, data } = {}) => {
      return status === 0 && Array.isArray(data) ? data : [];
    });
}

export default function Events({ search }) {
  const { state, update } = useStore();
  const { showDetails } = state;
  const [events, setEvents] = useState([]);
  const [loading, setLoading] = useState(false);
  const searchRef = useRef("");
  const { t } = useTranslation();

  const handleFetchEvents = useCallback(
    (search = "") => {
      searchRef.current = search;
      setLoading(true);

      fetchEvents(search)
        .then((events) => {
          if (events) {
            setEvents(events);
            update((draft) => {
              draft.isLoggedIn = "true";
            });
          }
        })
        .finally(() => {
          setLoading(false);
        });
    },
    [update]
  );

  useEffect(() => {
    if (showDetails) {
      goTo(Details);
    }
    handleFetchEvents(search);
  }, [showDetails, handleFetchEvents]);

  const handleClick = useCallback((event) => {
    goTo(EventForm, {
      event: {
        ...event,
        startDateTime: moment(event.startDateTime).format("YYYY-MM-DDThh:mm"),
        endDateTime: moment(event.endDateTime).format("YYYY-MM-DDThh:mm"),
      },
      isBack: true,
      search: searchRef.current,
    });
  }, []);

  useAddButton();

  return (
    <>
      {loading && <LoadingView position="fixed" />}
      <SearchInput onChange={handleFetchEvents} initialValue={search} translate={t} />
      <Box display="flex">
        <EventList events={events} onClick={handleClick} translate={t} />
      </Box>
    </>
  );
}
