const SERVER_URL_KEY = "ax_server_url";
const XCSRF_KEY = "ax_xcsrf";
const LOGGEDIN_KEY = "ax_loggedin";

export function getItem(key) {
  try {
    return localStorage.getItem(key);
  } catch (err) {}
}

export function setItem(key, value) {
  try {
    return localStorage.setItem(key, value);
  } catch (err) {}
}

export function removeItem(key) {
  try {
    return localStorage.removeItem(key);
  } catch (err) {}
}

export const getServerURL = getItem.bind(null, SERVER_URL_KEY);
export const setServerURL = setItem.bind(null, SERVER_URL_KEY);
export const removeServerURL = removeItem.bind(null, SERVER_URL_KEY);

export const getXCSRF = getItem.bind(null, XCSRF_KEY);
export const setXCSRF = setItem.bind(null, XCSRF_KEY);
export const removeXCSRF = removeItem.bind(null, XCSRF_KEY);

export const getLoggedIn = getItem.bind(null, LOGGEDIN_KEY);
export const setLoggedIn = setItem.bind(null, LOGGEDIN_KEY);
export const removeLoggedIn = removeItem.bind(null, LOGGEDIN_KEY);
