import { useEffect } from "react";

import { useStore } from "../services/store";

export function useAddButton() {
  const { update } = useStore();

  useEffect(() => {
    function set(value) {
      update((draft) => {
        draft.showAddButton = value;
      });
    }
    set(true);
    return () => {
      set(false);
    };
  }, [update]);
}

export default useAddButton;
