import React, { useCallback, useEffect, useRef, useState } from "react";

import List from "@material-ui/core/List";
import ListItemText from "@material-ui/core/ListItemText";
import Box from "@material-ui/core/Box";
import { makeStyles } from "@material-ui/core/styles";
import { goTo } from "route-lite";

import { SearchInput, ListItem, LoadingView } from "../../components";
import rest from "../../services/rest";
import { getServerURL } from "../../services/storage";
import { useStore } from "../../services/store";
import Details from "../details";
import ContactForm from "./contact-form";
import { useAddButton } from "../../hooks";
import { useTranslation } from "../../services/translate";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
    backgroundColor: theme.palette.background.paper,
  },
}));

function fetchContacts(search) {
  return rest
    .search("com.axelor.apps.base.db.Partner", {
      fields: [
        "emailAddress",
        "emailAddress.address",
        "simpleFullName",
        "name",
        "firstName",
        "mainPartner",
        "titleSelect",
        "fixedPhone",
        "jobTitleFunction",
        "mobilePhone",
        "mainAddress",
        "user",
        "team",
        "description",
        "companySet",
        "picture",
        "id",
        "version",
      ],
      sortBy: ["firstName"],
      data: {
        _domain:
          "self.isContact = true and (lower(self.emailAddress.address) like :search or lower(self.name) like :search or lower(self.firstName) like :search)",
        _domainContext: {
          search: `%${search.toLowerCase()}%`,
        },
      },
    })
    .then(({ data, status } = {}) => {
      return status === 0 && Array.isArray(data) ? data : [];
    });
}

export default function Contacts({ search }) {
  const classes = useStyles();
  const { state, update } = useStore();
  const { showDetails } = state;
  const [contacts, setContacts] = useState([]);
  const [loading, setLoading] = useState(false);
  const searchRef = useRef("");
  const { t } = useTranslation();
  const url = getServerURL();

  const getImageURL = useCallback(
    (id) => {
      return `${url}/ws/rest/com.axelor.meta.db.MetaFile/${id}/content/download`;
    },
    [url]
  );

  const handleFetchContacts = useCallback(
    (search = "") => {
      searchRef.current = search;
      setLoading(true);

      fetchContacts(search)
        .then((contacts) => {
          if (contacts) {
            setContacts(contacts);
            update((draft) => {
              draft.isLoggedIn = "true";
            });
          }
        })
        .finally(() => {
          setLoading(false);
        });
    },
    [update]
  );

  useEffect(() => {
    if (showDetails) {
      goTo(Details);
    }

    handleFetchContacts(search);
  }, [handleFetchContacts, showDetails]);

  useAddButton();

  return (
    <>
      {loading && <LoadingView position="fixed" />}
      <SearchInput onChange={handleFetchContacts} initialValue={search} translate={t} />
      <Box display="flex">
        <List className={classes.root}>
          {contacts && Boolean(contacts.length) ? (
            contacts.map((contact, i) => (
              <React.Fragment key={i}>
                <ListItem
                  subtitle={contact?.["emailAddress.address"]}
                  title={contact.simpleFullName}
                  image={getImageURL(contact?.picture?.id)}
                  onClick={() => {
                    goTo(ContactForm, {
                      contact: {
                        ...contact,
                        emailAddress: {
                          ...contact.emailAddress,
                          address: contact["emailAddress.address"],
                        },
                      },
                      isBack: true,
                      search: searchRef.current,
                    });
                  }}
                  divider
                />
              </React.Fragment>
            ))
          ) : (
            <ListItem>
              <ListItemText primary={t("No contacts available")} />
            </ListItem>
          )}
        </List>
      </Box>
    </>
  );
}
