import React, { useState, useCallback, useContext, useMemo, useEffect } from "react";
import produce from "immer";

import { getLoggedIn } from "./storage";

const StoreContext = React.createContext();

async function fetchMailDetails({ isCompose }) {
  return new Promise((resolve) => {
    Office.onReady(() => {
      const list = [];
      let office365Id;
      let mailInfo = {};
      if (Office.context.mailbox) {
        const item = Office.context.mailbox.item;
        office365Id = item.itemId;
        if (item) {
          if (!isCompose) {
            list.push(...item.cc, ...item.bcc, ...item.to, item.from);
          } else {
            list.push(item.from);
          }
          mailInfo.subject = item.subject;
        }
      }

      if (list[0] && list[0].emailAddress) {
        resolve({
          email: list[0].emailAddress,
          name: list[0].displayName,
          office365Id: office365Id,
          mailList: list,
          mailInfo: mailInfo,
        });
        return;
      }

      resolve();
    });
  });
}

function Store({ children }) {
  const isCompose = window.location.search.includes("mode=compose");

  const [state, setState] = useState({
    email: null,
    name: null,
    isLoggedIn: getLoggedIn() || "true",
    moduleTitle: isCompose ? "Templates" : "Contacts",
    menu: 1,
    isCompose,
    loading: true,
    showDetails: true,
    showAddButton: false,
  });

  const update = useCallback((cb) => {
    setState(produce(cb));
  }, []);

  useEffect(() => {
    fetchMailDetails({ isCompose })
      .then((details) => {
        if (details) {
          update((draft) => {
            draft.email = details.email;
            draft.name = details.name;
            draft.office365Id = details.office365Id;
            draft.mailList = details.mailList;
            draft.mailInfo = details.mailInfo;
          });
        }
      })
      .finally(() => {
        update((draft) => {
          draft.loading = false;
        });
      });
  }, [update]);

  const value = useMemo(() => ({ state, update }), [state, update]);

  return <StoreContext.Provider value={value}>{children}</StoreContext.Provider>;
}

export const useStore = () => useContext(StoreContext);

export default Store;
